import React, { useEffect, useState, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../../AuthProvider";
import styles from "./QnaList.module.css";
import Paging from "../../components/common/Paging";
import InsertButton from "../../components/common/button/InsertButton";

const QnaList = () => {
  const { isLoggedIn, isAuthInitialized, secureApiRequest, role } =
    useContext(AuthContext); // AuthContext   API   
  const navigate = useNavigate();

  const [qnaList, setQnaList] = useState([]); // QnA  
  const [totalItems, setTotalItems] = useState(0); //  QnA   
  const [currentPage, setCurrentPage] = useState(1); //   
  const [isLoading, setIsLoading] = useState(false); //  
  const [error, setError] = useState(null); //  
  const itemsPerPage = 10; //     

  const userUuid = localStorage.getItem("uuid"); // localStorage UUID 

  //   
  useEffect(() => {
    if (isAuthInitialized && !isLoggedIn) {
      console.log("  .   .");
      alert(" ."); //  wwww
      navigate("/login"); //      
    }
  }, [isLoggedIn, isAuthInitialized, navigate]);

  // QnA   
  const fetchQnaList = async (page = 1) => {
    if (!isLoggedIn) {
      navigate("/login"); //        
      return;
    }

    setIsLoading(true); //   
    setError(null); //   

    try {
      console.log(`QnA   - : ${page}`);
      const response = await secureApiRequest(
        `/qna?page=${page}&size=${itemsPerPage}` //   URL  
      );

      console.log(" :", response.data);

      //     
      setQnaList(response.data.list || []);
      setTotalItems(response.data.paging?.totalItems || 0);
      setCurrentPage(page);
    } catch (err) {
      console.error("QnA   :", err);
      setError("    .");
    } finally {
      setIsLoading(false);
    }
  };

  //     
  useEffect(() => {
    fetchQnaList(currentPage); //     
  }, [currentPage]);

  const handleInsertClick = () => {
    navigate("/qna/insert"); //    
  };

  const handleDetailClick = (qNo) => {
    console.log("Navigating to:", `/qna/detail/${qNo}`);
    navigate(`/qna/detail/${qNo}`); //   
  };

  if (error) {
    return <p className={styles.error}>{error}</p>; //      
  }

  return (
    <div className={styles.container}>
      <h1 className={styles.title}>QnA</h1>
      {isLoading ? (
        <p>  ...</p> //    
      ) : (
        <>
          <table className={styles["qna-table"]}>
            <thead>
              <tr>
                <th>No</th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {qnaList.length > 0 ? (
                qnaList.map((qna, index) => {
                  const isSecret = qna.qisSecret === "Y"; //  
                  const isAccessible =
                    role === "ADMIN" ||
                    qna.qwriter === userUuid ||
                    qna.uuid === userUuid; // ,   UUID   

                  //    
                  const canAccess = !isSecret || isAccessible;

                  return (
                    <tr
                      key={`${qna.qno}-${index}`} //   
                      onClick={() => canAccess && handleDetailClick(qna.qno)} //     
                      className={
                        !canAccess && isSecret ? styles.disabledRow : ""
                      }
                      style={{
                        cursor: canAccess ? "pointer" : "not-allowed",
                      }}
                    >
                      <td>{index + 1 + (currentPage - 1) * itemsPerPage}</td>
                      <td>
                        {!canAccess && isSecret ? "" : qna.qtitle}
                      </td>
                      <td>{!canAccess && isSecret ? "-" : qna.qwriter}</td>
                      <td>{new Date(qna.qWDate).toLocaleDateString()}</td>
                    </tr>
                  );
                })
              ) : (
                <tr>
                  <td colSpan="4"> .</td>
                </tr>
              )}
            </tbody>
          </table>
          <Paging
            totalItems={totalItems} //    
            itemsPerPage={itemsPerPage} //    
            currentPage={currentPage} //   
            onPageChange={(page) => setCurrentPage(page)} //     
          />
        </>
      )}
      {role === "USER" && (
        <div className={styles.buttonContainer}>
          <InsertButton onClick={handleInsertClick} label=" " />{" "}
          {/*    */}
        </div>
      )}
    </div>
  );
};

export default QnaList;
