import React, { useState } from "react";
import { useNavigate } from "react-router-dom"; //   
import styles from "./FaceRegistration.module.css";

function FaceRegistration() {
  const [streamUrl, setStreamUrl] = useState(null); //  URL  
  const [intervalId, setIntervalId] = useState(null); // setInterval ID 
  const [isRegistering, setIsRegistering] = useState(false); //   
  const [isSaved, setIsSaved] = useState(false); // DB   
  const navigate = useNavigate(); //  

  const handleRegisterClick = () => {
    const uuid = localStorage.getItem("uuid"); //  uuid 
    if (uuid) {
      setIsRegistering(true); //   ".." 
      const url = `http://127.0.0.1:8000/faceRegistration/streamVideo?uuid=${uuid}`;
      setStreamUrl(url); //  URL 

      // 1 DB   
      const id = setInterval(async () => {
        try {
          const response = await fetch("http://127.0.0.1:8000/faceRegistration/checkSaveStatus");
          const data = await response.json();
          console.log("DB  :", data.db_save_status);

          if (data.db_save_status) {
            // DB   true 
            clearInterval(id); // setInterval 
            setIntervalId(null); // interval ID 
            setIsSaved(true); //    

            // 2  /updateUser  
            setTimeout(() => {
              navigate("/updateUser");
            }, 2000);
          }
        } catch (error) {
          console.error("DB      :", error);
        }
      }, 1000);
      setIntervalId(id); // interval ID 
    } else {
      alert("UUID .  ."); // UUID   
    }
  };

  return (
    <div className={styles.container}>
      <h2>  </h2>
     
      <div>
        {streamUrl && <img src={streamUrl} alt="Streaming" />}
      </div>
      {isSaved && (
        <p className={styles.successMessage}> .  .</p>
      )} {/*     */}
      <button
        className={styles.reBtn}
        onClick={handleRegisterClick}
        disabled={isRegistering} //    
      >
        {isRegistering ? "..." : ""} {/*    */}
      </button>
    </div>
  );
}

export default FaceRegistration;
