import React, { useEffect, useState, useContext } from "react";
import { AuthContext } from "../../AuthProvider";
import MypageSubMenubar from "../../components/common/subMenubar/MypageSubMenubar";
import styles from "./MyTicketList.module.css";

function MyTicketList() {
  const { secureApiRequest } = useContext(AuthContext);
  const [tickets, setTickets] = useState([]); //  
  const [selectedTicket, setSelectedTicket] = useState(null); //   
  const [isModalOpen, setIsModalOpen] = useState(false); //  

  useEffect(() => {
    const fetchTickets = async () => {
      const storedUuid = localStorage.getItem("uuid");

      if (!storedUuid) {
        console.error("  UUID .");
        return;
      }

      try {
        const response = await secureApiRequest(`/mypage/ticket/${storedUuid}`, {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
          },
        });

        if (response.status < 200 || response.status >= 300) {
          console.error(`API  :   ${response.status}`);
          return;
        }

        let ticketData = response.data;

        // :        ,   
        ticketData = ticketData.sort((a, b) => {
          if (a.ticketCount > 0 && b.ticketCount === 0) return -1; //    
          if (a.ticketCount === 0 && b.ticketCount > 0) return 1; //    
          return new Date(b.ticketStartDate) - new Date(a.ticketStartDate); //   
        });

        setTickets(ticketData);
      } catch (error) {
        console.error("API    :", error);
      }
    };

    fetchTickets();
  }, [secureApiRequest]);

  const openModal = (ticket) => {
    setSelectedTicket(ticket);
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setSelectedTicket(null);
    setIsModalOpen(false);
  };

  const handleRefund = (ticketKey) => {
    console.log(` :   ${ticketKey}`);
    //    
  };

  return (
    <div>
      <MypageSubMenubar />
      <div className={styles.container}>
        <h1 className={styles.title}></h1>
        <h2 className={styles.subTitle}> </h2>
        {tickets.length === 0 ? (
          <p className={styles.noTickets}>    .</p>
        ) : (
          <table className={styles.ticketTable}>
            <thead>
              <tr>
                <th> </th>
                <th> </th>
                <th> </th>
                <th> </th>
                <th> </th>
              </tr>
            </thead>
            <tbody>
              {tickets.map((ticket) => (
                <tr key={ticket.ticketKey} onClick={() => openModal(ticket)}>
                  <td>{ticket.ticketName}</td>
                  <td>
                    {ticket.ticketCount} / {ticket.prodNumberOfTime || ""}
                  </td>
                  <td>{ticket.ticketAmount.toLocaleString()}</td>
                  <td>{new Date(ticket.ticketStartDate).toLocaleDateString()}</td>
                  <td>{new Date(ticket.ticketEndDate).toLocaleDateString()}</td>
                </tr>
              ))}
            </tbody>
          </table>
        )}

        {isModalOpen && selectedTicket && (
          <div className={styles.modal}>
            <div className={styles.modalContent}>
              <h3> </h3>
              <p> : {selectedTicket.ticketName}</p>
              <p>
                  :{" "}
                {new Date(selectedTicket.ticketStartDate).toLocaleDateString()}
              </p>
              <p>
                 :{" "}
                {new Date(selectedTicket.ticketEndDate).toLocaleDateString()}
              </p>
              <p> : {selectedTicket.ticketAmount.toLocaleString()}</p>
              <p>
                  : {selectedTicket.ticketCount} /{" "}
                {selectedTicket.prodNumberOfTime || ""}
              </p>
              {/*     */}
              {selectedTicket.ticketCount === selectedTicket.prodNumberOfTime && (
                <button
                  className={styles.refundButton}
                  onClick={() => handleRefund(selectedTicket.ticketKey)}
                >
                  
                </button>
              )}
              <button onClick={closeModal} className={styles.closeButton}>
                
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default MyTicketList;
