import React, { useEffect, useRef, useContext } from "react";
import { useNavigate } from "react-router-dom";
import style from "./GoogleLink.module.css";
import googleLogo from "../../assets/images/google.png";
import { AuthContext } from "../../AuthProvider";

function GoogleLink() {
  const navigate = useNavigate();
  const { secureApiRequest } = useContext(AuthContext);
  const isRequestSent = useRef(false); //   

  useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search);
    const code = urlParams.get("code");
    const uuid = localStorage.getItem("uuid");

    if (!uuid) {
      alert("  .");
      navigate("/login");
      return;
    }

    if (!code) {
      alert("Google   .");
      navigate("/error");
      return;
    }

    const sendCodeToServer = async () => {
      if (isRequestSent.current) return; //   
      isRequestSent.current = true; //  

      try {
        const response = await secureApiRequest("/google/link", {
          method: "POST",
          body: JSON.stringify({ code, uuid }),
          headers: {
            "Content-Type": "application/json",
          },
        });

        const responseData = response.data;
        if (response.status === 200) {
          alert(responseData.message || "Google    .");
          navigate("/updateUser");
        } else {
          alert(responseData.error || "Google   .");
        }
      } catch (error) {
        console.error("Google   :", error);
        alert("Google  .  .");
        navigate("/updateUser");
      }
    };

    sendCodeToServer();
  }, [navigate, secureApiRequest]);

  return (
    <div className={style.container}>
      <img src={googleLogo} className={style.googleLogo} alt="Google " />
      <div>Google   ...</div>
    </div>
  );
}

export default GoogleLink;
