import React, { useState, useEffect, useRef } from "react";
import style from "./Signup.module.css";
import apiClient from "../../utils/axios";
import { useNavigate } from "react-router-dom";

function Signup() {
  const navigate = useNavigate(); //  navigate 
  const [userId, setUserId] = useState("");
  const [isIdAvailable, setIsIdAvailable] = useState(false);
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [confirmPasswordError, setConfirmPasswordError] = useState("");
  const [isPasswordValid, setIsPasswordValid] = useState(true); //   
  const passwordInputRef = useRef(null);
  const [birthDate, setBirthDate] = useState("");
  const [birthDateError, setBirthDateError] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [phoneError, setPhoneError] = useState("");
  const [email, setEmail] = useState("");
  const [emailError, setEmailError] = useState("");
  const [isEmailAvailable, setIsEmailAvailable] = useState(false); //    
  const [verificationCode, setVerificationCode] = useState("");
  const [verificationError, setVerificationError] = useState("");
  const [isVerified, setIsVerified] = useState(false); //   
  const [allChecked, setAllChecked] = useState(false); //   
  const [serviceAgreement, setServiceAgreement] = useState(false); //    
  const [privacyPolicy, setPrivacyPolicy] = useState(false); //      
  const [marketingConsent, setMarketingConsent] = useState(false); //     
  const [name, setName] = useState(""); //   
  const [gender, setGender] = useState("male"); //   (: )
  const [verificationTime, setVerificationTime] = useState(0); //   ( )
  const [isVerificationActive, setIsVerificationActive] = useState(false); //   
  const [isPhoneAvailable, setIsPhoneAvailable] = useState(false); //     
  const [phoneMessage, setPhoneMessage] = useState(""); //  

  const handleAllCheck = (e) => {
    const checked = e.target.checked;

    //    
    setAllChecked(checked);
    setServiceAgreement(checked);
    setPrivacyPolicy(checked);
    setMarketingConsent(checked);
  };

  const handleIndividualCheck = (type, checked) => {
    if (type === "service") {
      setServiceAgreement(checked);
    } else if (type === "privacy") {
      setPrivacyPolicy(checked);
    } else if (type === "marketing") {
      setMarketingConsent(checked);
    }

    //     (     true)
    setAllChecked(
      (type === "service" ? checked : serviceAgreement) &&
        (type === "privacy" ? checked : privacyPolicy) &&
        (type === "marketing" ? checked : marketingConsent)
    );
  };

  const handleUserIdChange = (e) => {
    setUserId(e.target.value);
    setIsIdAvailable(false);
  };

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
    setIsPasswordValid(true); //      
  };

  const handleConfirmPasswordChange = (e) => {
    setConfirmPassword(e.target.value);
  };

  const validatePassword = () => {
    const passwordRegex =
      /^(?=.*[A-Za-z])(?=.*[\d@$!%*?&])[A-Za-z\d@$!%*?&]{8,16}$/;

    //      `alert` 
    if (!passwordRegex.test(password)) {
      if (isPasswordValid) {
        //      alert  
        alert(
          " 8~16,  , ,   2   ."
        );
      }
      setPassword(""); //  
      setIsPasswordValid(false); //  
      passwordInputRef.current.focus(); //  
    } else {
      setIsPasswordValid(true); //     
    }
  };

  useEffect(() => {
    if (confirmPassword) {
      if (password !== confirmPassword) {
        setConfirmPasswordError(" .");
      } else {
        setConfirmPasswordError("");
      }
    }
  }, [password, confirmPassword]);

  const handleBirthDateChange = (e) => {
    const value = e.target.value;

    if (/^\d*$/.test(value)) {
      setBirthDate(value);

      if (value.length !== 8) {
        setBirthDateError("* 8 ");
      } else {
        setBirthDateError("");
      }
    }
  };

  const handlePhoneNumberChange = (e) => {
    const value = e.target.value;

    if (/^\d*$/.test(value)) {
      setPhoneNumber(value);
      setPhoneError("");
    } else {
      setPhoneError("*   ");
    }
  };

  const handlePhoneCheck = async () => {
    if (phoneNumber.length === 0) {
      setPhoneError("* ");
      return;
    }

    try {
      const response = await apiClient.post(
        "/users/checkPhoneNumber",
        {
          phoneNumber,
        }
      );

      if (response.data === "dup") {
        alert("   .");
        setPhoneMessage(""); //    
        setIsPhoneAvailable(false);
      } else {
        alert("  .");
        setPhoneMessage("*  .");
        setIsPhoneAvailable(true);
      }
    } catch (error) {
      console.error("    :", error);
      alert("    .");
    }
  };

  const handleEmailChange = (e) => {
    setEmail(e.target.value);
    setIsEmailAvailable(false);
  };

  const checkDuplicateEmail = async () => {
    if (!email) {
      setEmailError("* ");
      return;
    }

    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(email)) {
      setEmailError("*   ");
      return;
    }

    try {
      const response = await apiClient.post(
        "/users/checkEmail",
        {
          email,
        }
      );

      if (response.data === "dup") {
        setEmailError("*   .");
        setIsEmailAvailable(false);
      } else {
        setEmailError("");
        setIsEmailAvailable(true);
      }
    } catch (error) {
      console.error("    :", error);
      alert("    .");
    }
  };

  //    useEffect
  useEffect(() => {
    let timer;
    if (verificationTime > 0) {
      timer = setInterval(() => {
        setVerificationTime((prevTime) => prevTime - 1);
      }, 1000);
    } else if (verificationTime === 0 && isVerificationActive) {
      //     
      setIsVerificationActive(false);
    }
    return () => clearInterval(timer);
  }, [verificationTime, isVerificationActive]);

  //      
  const handleSendVerificationEmail = async () => {
    if (!isEmailAvailable) {
      alert("    .");
      return;
    }

    try {
      const response = await apiClient.post(
        "/users/sendVerificationEmail",
        {
          email,
        }
      );

      alert(response.data);

      //     
      setVerificationTime(180); // 3 = 180
      setIsVerificationActive(true); //  
    } catch (error) {
      console.error("     :", error);
      alert("     .");
    }
  };

  const handleVerifyCode = async () => {
    if (!verificationCode) {
      setVerificationError("* .");
      return;
    }

    try {
      const response = await apiClient.post(
        "/users/verifyCode",
        {
          email,
          code: verificationCode,
        }
      );

      alert(response.data);
      setIsVerified(true);
      setVerificationError("");
    } catch (error) {
      console.error("     :", error);
      setVerificationError("*  .");
    }
  };

  const checkDuplicateId = async () => {
    if (!userId) {
      alert(" .");
      return;
    }

    if (!/^[a-z0-9]{6,20}$/.test(userId)) {
      alert(" 6~20,      .");
      return;
    }

    try {
      const response = await apiClient.post(
        "/users/checkuserId",
        {
          users: userId,
        }
      );

      if (response.data === "dup") {
        alert("   .");
        setIsIdAvailable(false);
      } else {
        alert("  .");
        setIsIdAvailable(true);
      }
    } catch (error) {
      console.error("    :", error);
      alert("    .");
    }
  };

  const handleSignup = async () => {
    //  
    if (!isIdAvailable) {
      alert("  .");
      return;
    }
    if (!isPasswordValid || confirmPasswordError) {
      alert(" .");
      return;
    }
    if (!isEmailAvailable || !isVerified) {
      alert("     .");
      return;
    }
    if (!birthDate || birthDateError) {
      alert("  .");
      return;
    }
    if (!phoneNumber || phoneError) {
      alert(" .");
      return;
    }
    if (!serviceAgreement || !privacyPolicy) {
      alert("  .");
      return;
    }

    //   
    //   
    const formattedBirthDate = `${birthDate.slice(0, 4)}-${birthDate.slice(
      4,
      6
    )}-${birthDate.slice(6, 8)}`;
    // gender  "M"  "F" 
    const genderValue = gender === "male" ? "M" : "F";
    const signupData = {
      userId: userId,
      userPw: password,
      userDefaultEmail: email,
      userName: name,
      userBirthday: formattedBirthDate,
      userPhone: phoneNumber,
      userGender: genderValue,
    };

    //     
    console.log(" :", signupData);

    try {
      const response = await apiClient.post(
        "/users/signup",
        signupData
      );

      if (response.data === "success") {
        alert(" .");
        //      
        navigate("/login"); // '/login' 
      } else {
        alert(" .  .");
      }
    } catch (error) {
      console.error("   :", error);
      alert("   .");
    }
  };

  return (
    <div className={style.signupContainer}>
      <div className={style.signupTitle}>
        ! <br />    JOBIS !
      </div>
      <hr className={style.topLine} />
      <table className={style.signupTable}>
        <tbody>
          <tr>
            <td className={style.title}></td>
            <td className={style.sub}>
              <input
                type="text"
                placeholder=""
                value={userId}
                onChange={handleUserIdChange}
                disabled={isIdAvailable}
              />
              <br /> 6~20,  ,  
              {isIdAvailable && (
                <span style={{ color: "green", marginLeft: "10px" }}>
                  *  .
                </span>
              )}
            </td>
            <td>
              <button
                className={style.checkButton2}
                type="button"
                onClick={checkDuplicateId}
                disabled={isIdAvailable}
              >
                
              </button>
            </td>
          </tr>
          <tr>
            <td className={style.title}></td>
            <td className={style.sub}>
              <input
                type="password"
                placeholder=""
                value={password}
                onChange={handlePasswordChange}
                onBlur={validatePassword} //    
                ref={passwordInputRef}
              />
              <br /> 8~16,  , ,   2  
            </td>
          </tr>

          <tr>
            <td className={style.title}></td>
            <td>
              <input
                type="password"
                placeholder=" "
                value={confirmPassword}
                onChange={handleConfirmPasswordChange}
              />
              <br />
              {confirmPasswordError && (
                <span style={{ color: "red" }}>{confirmPasswordError}</span>
              )}
            </td>
          </tr>
          <tr>
            <td className={style.title}></td>
            <td>
              <input
                type="text"
                placeholder=""
                value={email}
                onChange={handleEmailChange}
                disabled={isVerified} //      
              />
              <br />
              {emailError && (
                <span style={{ color: "red", marginLeft: "10px" }}>
                  {emailError}
                </span>
              )}
              {isEmailAvailable && !isVerified && (
                <span style={{ color: "green", marginLeft: "10px" }}>
                  *  .
                </span>
              )}
              {isVerified && (
                <span style={{ color: "green", marginLeft: "10px" }}>
                  *  .
                </span>
              )}
              {/*    */}
              {isVerificationActive && !isVerified && (
                <div
                  style={{
                    color: "red",
                    fontWeight: "bold",
                    marginTop: "10px",
                  }}
                >
                     : {Math.floor(verificationTime / 60)}:
                  {String(verificationTime % 60).padStart(2, "0")} 
                </div>
              )}
            </td>
            <td>
              <button
                className={style.checkButton}
                type="button"
                onClick={checkDuplicateEmail}
                disabled={isVerified || isEmailAvailable} //     
                style={{
                  backgroundColor: isVerified ? "#d3d3d3" : "",
                  cursor: isVerified ? "not-allowed" : "pointer",
                }}
              >
                
              </button>
            </td>
            <td>
              <button
                className={style.checkButton}
                type="button"
                onClick={handleSendVerificationEmail}
                disabled={!isEmailAvailable || isVerified} //     
                style={{
                  backgroundColor: isVerified ? "#d3d3d3" : "",
                  cursor: isVerified ? "not-allowed" : "pointer",
                }}
              >
                
              </button>
            </td>
          </tr>
          <tr>
            <td className={style.title}></td>
            <td>
              <input
                type="text"
                placeholder=" "
                value={verificationCode}
                onChange={(e) => setVerificationCode(e.target.value)}
                disabled={isVerified} //      
              />
              <br />
              {isVerified ? (
                <span style={{ color: "green", marginLeft: "10px" }}>
                  *  .
                </span>
              ) : (
                verificationError && (
                  <span style={{ color: "red", marginLeft: "10px" }}>
                    {verificationError}
                  </span>
                )
              )}
            </td>
            <td>
              <button
                className={style.checkButton}
                type="button"
                onClick={handleVerifyCode}
                disabled={isVerified} //     
                style={{
                  backgroundColor: isVerified ? "#d3d3d3" : "",
                  cursor: isVerified ? "not-allowed" : "pointer",
                }}
              >
                
              </button>
            </td>
          </tr>

          <tr>
            <td className={style.title}></td>
            <td>
              <input
                type="text"
                placeholder=" "
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            </td>
          </tr>

          <tr>
            <td className={style.title}></td>
            <td className={style.sub}>
              <input
                type="text"
                placeholder=" 8"
                value={birthDate}
                onChange={handleBirthDateChange}
                maxLength={8}
              />
              <br />  : 20010628
              {birthDateError && (
                <span style={{ color: "red", marginLeft: "10px" }}>
                  {birthDateError}
                </span>
              )}
            </td>
            <td className={style.radioContainer}>
              <div className={style.male}>
                <input
                  type="radio"
                  id="male"
                  name="gender" //  name   
                  value="male" // value   
                  checked={gender === "male"} //     
                  onChange={(e) => setGender(e.target.value)} //  
                  className={style.hiddenRadio}
                />
                <label htmlFor="male" className={style.customRadio}></label>
              </div>
              <div className={style.female}>
                <input
                  type="radio"
                  id="female"
                  name="gender" //  name   
                  value="female" // value   
                  checked={gender === "female"} //     
                  onChange={(e) => setGender(e.target.value)} //  
                  className={style.hiddenRadio}
                />
                <label htmlFor="female" className={style.customRadio}></label>
                
              </div>
            </td>
          </tr>
          <tr>
            <td className={style.title}></td>
            <td className={style.sub}>
              <input
                type="text"
                placeholder="  '-' "
                value={phoneNumber}
                onChange={handlePhoneNumberChange}
                maxLength={11}
                disabled={isPhoneAvailable} //    
              />
              <br /> : 01012345678
               <span style={{ color: isPhoneAvailable ? "green" : "red" }}>
                              {phoneMessage}
                            </span>
              {phoneError && (
                <span style={{ color: "red", marginLeft: "10px" }}>
                  {phoneError}
                </span>
              )}
            </td>
            <td>
              <button
                className={style.checkButton2}
                type="button"
                onClick={handlePhoneCheck}
              >
                
              </button>
            </td>
          </tr>
        </tbody>
      </table>
      <hr className={style.bottomLine} />
      <div className={style.agreeCheck}>
        <div className={style.allCheck}>
          <input
            type="checkbox"
            checked={allChecked} //   
            onChange={handleAllCheck} //    
          />{" "}
              
        </div>
        <hr className={style.checkLine} />
        <div>
          <div>
            <input
              type="checkbox"
              checked={serviceAgreement} //   
              onChange={(e) =>
                handleIndividualCheck("service", e.target.checked)
              } //    
            />{" "}
               ()
          </div>
          <div>
            <input
              type="checkbox"
              checked={privacyPolicy} //     
              onChange={(e) =>
                handleIndividualCheck("privacy", e.target.checked)
              } //    
            />{" "}
                  ()
          </div>
          <div>
            <input
              type="checkbox"
              checked={marketingConsent} //    
              onChange={(e) =>
                handleIndividualCheck("marketing", e.target.checked)
              } //    
            />{" "}
                ()
          </div>
        </div>
      </div>

      <button
        className={style.signupBtn}
        onClick={handleSignup} //   
      >
        
      </button>
    </div>
  );
}

export default Signup;
