import React, { useEffect, useState, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../../AuthProvider"; // AuthContext 
import styles from "./NoticeList.module.css"; // CSS Modules
import Paging from "../../components/common/Paging"; // Paging  
import InsertButton from "../../components/common/button/InsertButton"; // InsertButton  

function NoticeList() {
  const { isLoggedIn, isAuthInitialized, secureApiRequest, role } =
    useContext(AuthContext);
  const [noticeList, setNoticeList] = useState([]); //   
  const [totalItems, setTotalItems] = useState(0); //    
  const [currentPage, setCurrentPage] = useState(1); //   
  const [isLoading, setIsLoading] = useState(false); //  
  const [error, setError] = useState(null); //   

  const navigate = useNavigate();
  const itemsPerPage = 10; //     

  const handleMoveDetail = (no) => {
    navigate(`/notice/detail/${no}`); //    
  };

  // const fetchNoticeList = async (page = 1) => {
  //   setIsLoading(true); //  
  //   setError(null); //  

  //   console.log(`Fetching notices for page: ${page}`); //   

  //   try {
  //     //      
  //     console.log(`Notice   - : ${page}`);
  //     const response = await secureApiRequest(`/notice?page=${page}&size=10`, {
  //       method: "GET",
  //   });

  //     //    
  //     console.log("Notice   :", response.data);
  //     setNoticeList(response.data.list || []); // Review  
  //     setTotalItems(response.data.paging?.totalItems || 0); //    
  //     setCurrentPage(page); //   
  //   } catch (err) {
  //     console.error("Notice   :", err);
  //     setError("    ."); //   
  //   } finally {
  //     //   
  //     setIsLoading(false);
  //   }
  // };

  // const fetchNoticeList = async (page = 1) => {
  //   setIsLoading(true); //  
  //   setError(null); //  

  //   try {
  //     const response = await secureApiRequest(`/notice?page=${page}&size=10`, {
  //       method: "GET",
  //     });

  //     const filteredList = response.data.list.filter((notice) => notice.noticeIsDeleted === "N"); // isDeleted 
  //     setNoticeList(filteredList); //    
  //     setTotalItems(response.data.paging?.totalItems || 0); //    
  //     setCurrentPage(page); //   
  //   } catch (err) {
  //     console.error("Notice   :", err);
  //     setError("    .");
  //   } finally {
  //     setIsLoading(false); //  
  //   }
  // };

  const fetchNoticeList = async (page = 1) => {
    setIsLoading(true);
    setError(null);

    try {
      const response = await secureApiRequest(`/notice?page=${page}&size=10`, {
        method: "GET",
      });

      //    
      console.log("API  :", response.data);

      const filteredList = response.data.list.filter(
        (notice) => notice.noticeIsDeleted === "N"
      ); // 
      setNoticeList(filteredList);
      setTotalItems(response.data.paging.totalItems);
      setCurrentPage(page);
    } catch (err) {
      console.error("   :", err);
      setError("    .");
    } finally {
      setIsLoading(false);
    }
  };

  const handleMoveInsert = () => {
    navigate(`/notice/insert`); //    
    console.log("Navigating to insert page"); //  
  };

  useEffect(() => {
    if (!isAuthInitialized && !isLoggedIn) {
      console.log("  .   .");
      navigate("/login");
    } else {
      fetchNoticeList(currentPage); //     
    }
  }, [isLoggedIn, isAuthInitialized, navigate]);

  const handlePageChange = (page) => {
    console.log(`Page changed to: ${page}`); //   
    fetchNoticeList(page); //     
  };

  //       
  console.log("isLoading:", isLoading);
  console.log("Current notice list:", noticeList);

  function formatDate(dateString) {
    const date = new Date(dateString);
    const year = date.getUTCFullYear();
    const month = String(date.getUTCMonth() + 1).padStart(2, "0");
    const day = String(date.getUTCDate()).padStart(2, "0");
    return `${year}-${month}-${day}`;
  }

  return (
    <div className={styles.noticecontainer}>
      <h2 className={styles.noticetitle}></h2>
      {error && <div className={styles.errorMessage}>{error}</div>}{" "}
      {/*    */}
      {isLoading && (
        <div className={styles.loadingMessage}> ...</div>
      )}{" "}
      {/*    */}
      <table className={styles.noticetable}>
        <tbody>
          {noticeList.map((notice) => (
            <tr
              key={notice.noticeNo}
              onClick={() => handleMoveDetail(notice.noticeNo)}
              className={styles.noticerow}
            >
              <td>{notice.noticeTitle}</td>
              <td>{formatDate(notice.noticeWDate)}</td>
            </tr>
          ))}
        </tbody>
      </table>
      <Paging
        totalItems={totalItems}
        itemsPerPage={itemsPerPage}
        currentPage={currentPage}
        onPageChange={handlePageChange} //    
      />
      {role === "ADMIN" && (
        <div className={styles.buttonContainer}>
          <InsertButton onClick={handleMoveInsert} label=" " />
        </div>
      )}
    </div>
  );
}

export default NoticeList;
