import { loadTossPayments, ANONYMOUS } from "@tosspayments/tosspayments-sdk";
import React, { useEffect, useState,  useContext  } from 'react';
import { useParams } from 'react-router-dom';
import apiClient from "../../utils/axios";
import TicketSubMenubar from "../../components/common/subMenubar/TicketSubMenubar";

import { AuthContext } from "../../AuthProvider"; // AuthProvider Context 
import { useNavigate } from "react-router-dom";
import styles from './TicketList.module.css';

// @docs https://docs.tosspayments.com/sdk/v2/js#-
const clientKey = "test_ck_BX7zk2yd8yLNpMR6DEQLrx9POLqK";
const customerKey = "7HUxbkFLek_EdGokYd3Cb";

function TicketList() {
    const { isLoggedIn, secureApiRequest, role } = useContext(AuthContext);
    const [isLoading, setIsLoading] = useState(false);  
    const navigate = useNavigate();
    const [products, setProducts] = useState([]); // Products   
    

    const [payment, setPayment] = useState(null);
    
    useEffect(() => {
        if (!isLoggedIn) {
            navigate("/login"); //   
        }
    }, [isLoggedIn,  navigate]);
    
    

    useEffect(() => {
        // Products  
        async function fetchProducts() {
            setIsLoading(true);
            try {
                const accessToken = localStorage.getItem("accessToken");
                const refreshToken = localStorage.getItem("refreshToken");
                
                const response = await apiClient.get("/products",  {
                    headers: {
                        Authorization: `Bearer ${accessToken}`, //   AccessToken 
                        refreshToken: `Bearer ${refreshToken}`, //   RefreshToken 
                    },
                });
                
                
                if (Array.isArray(response.data)) {
                    const validatedProducts = response.data.map((product) => ({
                        ...product,
                        prodAmount: Number(product.prodAmount), // 
                    }));
                    setProducts(validatedProducts);
                } else {
                    console.error("API response is not an array:", response.data);
                }
                console.log("products " + JSON.stringify(products) );
                console.log("response " + JSON.stringify(response.data) );
            } catch (error) {
                console.error("Failed to fetch products:", error);
            } finally {
                setIsLoading(false);
            }
        } fetchProducts();
    }, []);
    
   
    
    const [selectedPaymentMethod, setSelectedPaymentMethod] = useState(null);
    
    function selectPaymentMethod(method) {
        setSelectedPaymentMethod(method);
    }
    
    useEffect(() => {
        let isMounted = true; //  
        async function fetchPayment() {
            if (!isMounted) return; //     
            try {
                const tossPayments = await loadTossPayments(clientKey);
                const payment = tossPayments.payment({ customerKey });
                setPayment(payment);
            } catch (error) {
                console.error("Error fetching payment:", error);
            }
        }
        fetchPayment();
        return () => { isMounted = false }; // 
    }, [clientKey, customerKey]);
    
    // ------ ''     ------
    // @docs https://docs.tosspayments.com/sdk/v2/js#paymentrequestpayment
    //  
async function requestPayment(product) {
    console.log("Product data for payment:", product); // product  
    if (!payment) {
        console.error("Payment instance is not ready");
        return;
    }

    // amount  
    const amount = {
        currency: "KRW",
        value: product.prodAmount, //  product  
    };

    try {
        //    
        const response = await apiClient.post("/api/payments/request", {
            orderId: `ORDER_${product.prodNumber}_${Date.now()}`,
            orderName: product.prodName,
            amount: product.prodAmount,
            customerEmail: "customer123@gmail.com",
            customerName: "",
            customerMobilePhone: "01012341234",
        });

        // Toss SDK  
        await payment.requestPayment({
            method: "CARD",
            amount: amount,
            orderId: `ORDER_${product.prodNumber}_${Date.now()}`,
            orderName: product.prodName,
            successUrl: window.location.origin + "/paymentSuccess",
            failUrl: window.location.origin + "/payments/fail",
            customerEmail: "customer123@gmail.com",
            customerName: "",
            customerMobilePhone: "01012341234",
            card: {
                useEscrow: false,
                flowMode: "DEFAULT",
                useCardPoint: false,
                useAppCardOnly: false,
            },
        });

    } catch (error) {
        console.error("Payment request failed:", error);
    }
}


    if (isLoading) {
        return <div className={styles.loading}> ...</div>; //  
      }
    return(
        <div>
            <TicketSubMenubar/>
                <div className={styles.ticketContainer}>
                    {isLoading ? (
                        <p>   ...</p>
                    ) : (
                        products.length > 0 ? (
                            products.map((product) => (
                                <div key={product.prodNumber} className={styles.content}>
                                    <h3>{product.prodPeriod}</h3>
                                    <h4>{product.prodName}</h4>
                                    <p>{product.prodDescription}</p>
                                    <h2>{product.prodAmount} </h2>
                                    <button onClick={() => requestPayment(product)}></button>
                                </div>
                            ))
                        ) : (
                            <p> .</p>
                        )
                    )}
                </div>
        </div>
    );
            {/* {role === Admin &&(
                <button onClick={() => InsertProducts }> </button>
            )} */}
    
}

export default TicketList;