import React, { useState, useEffect, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../../AuthProvider";
import styles from "./FavoritesList.module.css";
import JobPostingSubMenubar from "../../components/common/subMenubar/JobPostingSubMenubar";
import FavoriteStar from "./FavoriteStar";

const FavoritesList = () => {
  const [favorites, setFavorites] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const navigate = useNavigate();
  const { secureApiRequest, isLoggedIn } = useContext(AuthContext);
  const [uuid, setUuid] = useState(null); // uuid 
  const [list,setList] = useState([]);

  //    uuid localStorage 
  useEffect(() => {
    const storedUuid = localStorage.getItem("uuid");
    if (storedUuid) {
      setUuid(storedUuid);
    }
  }, []);

  useEffect(() => {
    const fetchFavorites = async () => {
      setLoading(true);
      setError(null);
      try {
        if (!isLoggedIn) {
          setError("     .");
          return;
        }

        //   
        const favoritesResponse = await secureApiRequest(
          `/favorites/search?uuid=${uuid}`,
          { method: "GET" }
        );

        if (!favoritesResponse || !favoritesResponse.data) {
          setFavorites([]);
          return;
        }

        //        
        const favoritesWithDetails = await Promise.all(
          favoritesResponse.data.map(async (favorite) => {
            try {
              const jobResponse = await secureApiRequest(
                `/${favorite.jobPostingId}`,
                { method: "GET" }
              );
              console.log(jobResponse.data);
              const jobData = jobResponse?.data?.jobs?.job || null;

              return {
                ...favorite,
                jobTitle: jobData?.position?.title || " ",
                industry: jobData?.position?.industry?.name || " ",
                location: jobData?.position?.location?.name || " ",
                salary: jobData?.salary?.name || " ",
                company: jobData?.company?.detail?.name || " ",
              };
            } catch (error) {
              return {
                ...favorite,
                jobTitle: " ",
                industry: " ",
                location: " ",
                salary: " ",
                company: " ",
              };
            }
          })
        );

        setFavorites(favoritesWithDetails);
      } catch (err) {
        setError("     .");
      } finally {
        setLoading(false);
      }
    };

    if (isLoggedIn && uuid) {
      fetchFavorites();
    }
  }, [isLoggedIn, uuid, secureApiRequest]);

  //   
  const removeFavorite = async (jobPostingId) => {
    try {
      //   
      await secureApiRequest(
        `/favorites/delete?uuid=${uuid}&jobPostingId=${jobPostingId}`,
        { method: "DELETE" }
      );
      //     
      setFavorites((prevFavorites) =>
        prevFavorites.filter((fav) => fav.jobPostingId !== jobPostingId)
      );
    } catch (err) {
      alert("    .");
    }
  };

  //    
  const handleJobClick = (id) => {
    navigate(`/jobPosting/${id}`);
  };

  if (loading) return <p>   ...</p>;
  if (error) return <p>{error}</p>;

  return (
    <div>
      <JobPostingSubMenubar />
      <div className={styles.container}>
        <h2> </h2>
        <div className={styles.favoriteList}>
          {favorites.length > 0 ? (
            favorites.map((favorite) => (
              <div key={favorite.jobPostingId} className={styles.favoriteCard}>
                <div className={styles.cardHeader}>
                  <h3>{favorite.jobTitle}</h3>
                  <span className={styles.company}>{favorite.company}</span>
                </div>
                <div className={styles.cardContent}>
                  <p>: {favorite.industry}</p>
                  <p>: {favorite.location}</p>
                  <p>: {favorite.salary}</p>
                </div>
                <div className={styles.cardActions}>
                  <button
                    className={styles.viewButton}
                    onClick={() => handleJobClick(favorite.jobPostingId)}
                  >
                    
                  </button>
                  <FavoriteStar
                    initialFavorited={true} //  favorites   
                    onToggle={() => removeFavorite(favorite.jobPostingId)} //    
                  />
                </div>
              </div>
            ))
          ) : (
            <p>  .</p>
          )}
        </div>
      </div>
    </div>
  );
};

export default FavoritesList;
