import React, { useEffect, useRef, useContext } from "react";
import { useNavigate } from "react-router-dom";
import style from "./NaverLink.module.css";
import naverLogo from "../../assets/images/naver.png";
import { AuthContext } from "../../AuthProvider";

function NaverLink() {
  const navigate = useNavigate();
  const { secureApiRequest } = useContext(AuthContext);
  const isRequestSent = useRef(false); //     

  useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search);
    const code = urlParams.get("code");
    const state = urlParams.get("state");
    const uuid = localStorage.getItem("uuid");

    if (!uuid) {
      alert("  .");
      navigate("/login");
      return;
    }

    if (!code) {
      alert("   .");
      navigate("/error");
      return;
    }

    const sendCodeToServer = async () => {
      if (isRequestSent.current) return; //    
      isRequestSent.current = true; //   

      try {
        const response = await secureApiRequest("/naver/link", {
          method: "POST",
          body: JSON.stringify({ code, uuid }),
        });

        console.log("Axios  :", response);

        const responseData = response.data;
        if (response.status === 200 || responseData.success === "true") {
          alert(responseData.message || "  .");
          navigate("/updateUser");
        } else {
          alert(responseData.error || "  .");
        }
      } catch (error) {
        console.error("   :", error);
        alert("  .  .");
        navigate("/updateUser");
      }
    };

    sendCodeToServer();
  }, [navigate, secureApiRequest]);

  return (
    <div className={style.container}>
      <img src={naverLogo} className={style.naverLogo} alt=" " />
      <div>   ...</div>
    </div>
  );
}

export default NaverLink;
