import React, { createContext, useState, useEffect } from "react";
import apiClient from "../src/utils/axios"; // apiClient 

export const AuthContext = createContext();

const parseAccessToken = (token) => {
  if (!token) return null;
  try {
    const base64Url = token.split(".")[1];
    const base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
    const jsonPayload = decodeURIComponent(
      atob(base64)
        .split("")
        .map((c) => `%${("00" + c.charCodeAt(0).toString(16)).slice(-2)}`)
        .join("")
    );
    return JSON.parse(jsonPayload);
  } catch (error) {
    console.error("AccessToken  :", error);
    return null;
  }
};

export const AuthProvider = ({ children }) => {
  const [authInfo, setAuthInfo] = useState({
    isLoggedIn: false,
    role: "",
    username: "",
  });

  useEffect(() => {
    //       
    const accessToken = localStorage.getItem("accessToken");
    const refreshToken = localStorage.getItem("refreshToken");

    if (accessToken && refreshToken) {
      const parsedToken = parseAccessToken(accessToken);
      console.log("useEffect");
      console.log(parsedToken.role);
      console.log();
      console.log();
      if (parsedToken) {
        setAuthInfo({
          isLoggedIn: true,
          role: parsedToken.role,
          username: parsedToken.userName,
        });
      } else {
        logoutAndRedirect(); //      
      }
    }
  }, []);

  //       
  const updateTokens = (accessToken, refreshToken) => {
    if (accessToken) {
      localStorage.setItem("accessToken", accessToken);
      const parsedToken = parseAccessToken(accessToken);
      if (parsedToken) {
        setAuthInfo({
          isLoggedIn: true,
          role: parsedToken.role,
          username: parsedToken.userName,
        });
      } else {
        logoutAndRedirect(); //     
      }
    }
    if (refreshToken) {
      localStorage.setItem("refreshToken", refreshToken);
    }
  };

  //  API   
  const secureApiRequest = async (url, options = {}, retry = true) => {
    console.log("AuthProvider.js secureApiRequest ");
    const accessToken = localStorage.getItem("accessToken");
    const refreshToken = localStorage.getItem("refreshToken");

    if (!accessToken || !refreshToken) {
      throw new Error("AccessToken  RefreshToken .");
    }

    try {
      const method = options.method || "GET";
      const data = options.body || null;

      const response = await apiClient({
        url,
        method,
        headers: {
          Authorization: `Bearer ${accessToken}`,
          refreshToken: `Bearer ${refreshToken}`,
          ...options.headers,
        },
        data,
      });

      return response; //   
    } catch (error) {
      console.error("API   -  :", error.response?.status);
      console.error("API  :", error.response?.headers);
      console.error("API  :", error.response?.data);

      const tokenExpiredHeader = error.response?.headers["token-expired"];

      if (error.response?.status === 401 && retry) {
        // RefreshToken      
        if (tokenExpiredHeader === "RefreshToken") {
          const confirmExtend = window.confirm(
            " . ?"
          );
          if (confirmExtend) {
            console.log("  ")
            try {
              await handleReissueTokens(true); // RefreshToken 
              return secureApiRequest(url, options, false); // 
            } catch (refreshError) {
              console.error("  :", refreshError.response?.data);
              alert(" .  .");
              logoutAndRedirect();
            }
          } else {
            alert("  .  .");
            logoutAndRedirect();
          }
        }

        // AccessToken   RefreshToken AccessToken 
        if (tokenExpiredHeader === "AccessToken") {
          console.warn("AccessToken . RefreshToken   ...");
          try {
            await handleReissueTokens();
            return secureApiRequest(url, options, false); // 
          } catch (refreshError) {
            console.error(
              "AccessToken  :",
              refreshError.response?.data
            );
            logoutAndRedirect();
          }
        }
      }

      throw error; //   
    }
  };

  // AccessToken  RefreshToken  
  const handleReissueTokens = async (extendLogin = false) => {
    let accessToken = localStorage.getItem("accessToken")?.trim();
    let refreshToken = localStorage.getItem("refreshToken")?.trim();

    if (!accessToken || !refreshToken) {
      console.error("Reissue  :   .");
      alert(" .  .");
      logoutAndRedirect();
      return;
    }

    try {
      console.log("Reissue  - AccessToken:", accessToken);
      console.log("Reissue  - RefreshToken:", refreshToken);

      const response = await apiClient.post("/reissue", null, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
          RefreshToken: `Bearer ${refreshToken}`,
          ExtendLogin: extendLogin ? "true" : "false",
        },
      });

      console.log("Reissue  -  :", response.headers);
      updateTokens(
        response.headers["authorization"]?.split(" ")[1]?.trim(),
        response.headers["refresh-token"]?.split(" ")[1]?.trim()
      );
    } catch (error) {
      console.error("Reissue  -  :", error.response?.status);
      console.error("Reissue  -  :", error.response?.data);

      const expiredTokenType = error.response?.headers["token-expired"];
      if (
        expiredTokenType === "RefreshToken" ||
        error.response?.data === "Session Expired"
      ) {
        alert(" .  .");
        logoutAndRedirect();
      } else if (expiredTokenType === "AccessToken") {
        console.warn("AccessToken . RefreshToken   ...");
        return await handleReissueTokens();
      } else {
        console.error("Reissue     :", error.message);
        logoutAndRedirect();
      }
    }
  };

  //  
  const logoutAndRedirect = () => {
    if (!authInfo.isLoggedIn) return;
    localStorage.clear();
    setAuthInfo({ isLoggedIn: false, role: "", username: "" });
    window.location.href = "/";
  };

  return (
    <AuthContext.Provider
      value={{ ...authInfo, setAuthInfo, secureApiRequest }}
    >
      {children}
    </AuthContext.Provider>
  );
};
