import React, { useEffect, useState, useContext, useCallback } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { AuthContext } from "../../AuthProvider";
import styles from "./ReviewDetail.module.css";
import DeleteModal from "../../components/common/DeleteModal";
import downloadIcon from "../../assets/images/download_icon.png";

function ReviewDetail() {
    const accessToken = localStorage.getItem("accessToken");
    const { secureApiRequest } = useContext(AuthContext);
    const { rno } = useParams();
    const navigate = useNavigate();
    const [review, setReview] = useState(null);
    const [error, setError] = useState(null);
    const [isDeleteModalOpen, setDeleteModalOpen] = useState(false);
    const [filePreview, setFilePreview] = useState(null); //   URL

    const handleDelete = async () => {
        try {
            await secureApiRequest(`/review/${rno}/delete`, {
                method: "PUT",
            });
            alert(" .");
            navigate("/review"); //     
        } catch (error) {
            console.error("    :", error);
            alert("  .");
        }
    };
    
    const handleBack = () => {
        navigate("/review");
    };

    const isImageFile = (filePath) => {
        if (!filePath) return false;
        const imageExtensions = ["jpg", "jpeg", "png", "gif", "bmp"];
        const extension = filePath.split(".").pop().toLowerCase();
        return imageExtensions.includes(extension);
    };

    const fetchReviewDetail = useCallback(async () => {
        try {
            const response = await secureApiRequest(`/review/detail/${rno}`, {
                method: "GET",
            });
            const data = response.data || {};
            if (data.reviewPath && data.reviewPath.startsWith("http://localhost:8080/review/attachments/")) {
                data.reviewPath = data.reviewPath.replace("http://localhost:8080/review/attachments/", "");
            }
            setReview(data);

            if (data.reviewPath) {
                const previewUrl = await fetchImage(`http://localhost:8080/review/attachments/${data.reviewPath}`);
                setFilePreview(previewUrl);
            }
        } catch (error) {
            setError("    .");
            console.error("Error fetching review details:", error);
        }
    }, [rno, secureApiRequest]);

    const fetchImage = async (url) => {
        try {
            const response = await fetch(url);
            if (!response.ok) throw new Error("  ");
            const blob = await response.blob();
            return URL.createObjectURL(blob);
        } catch (error) {
            console.error("  :", error);
            return "/default-image.png"; //   
        }
    };

    const handleDownload = async () => {
        if (!review || !review.reviewPath) {
            alert("  .");
            return;
        }
        try {
            const fileUrl = `http://localhost:8080/review/attachments/${review.reviewPath}`;
            const response = await fetch(fileUrl, {
                method: "GET",
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                },
            });

            if (!response.ok) throw new Error("  ");

            const blob = await response.blob();
            const blobUrl = URL.createObjectURL(blob);

            const a = document.createElement("a");
            a.href = blobUrl;
            a.download = review.reviewPath.split("/").pop(); //   
            document.body.appendChild(a);
            a.click();
            document.body.removeChild(a);

            URL.revokeObjectURL(blobUrl);
        } catch (error) {
            console.error(" :", error);
            alert("  .");
        }
    };

    useEffect(() => {
        fetchReviewDetail();
    }, [fetchReviewDetail]);

    if (!review && !error) {
        return <div className={styles.loading}> ...</div>;
    }

    if (error) {
        return <div className={styles.error}>{error}</div>;
    }

    return (
        <div className={styles.reviewDetailContainer}>
            {/*   */}
            <DeleteModal
                isOpen={isDeleteModalOpen}
                onClose={() => setDeleteModalOpen(false)}
                onConfirm={() => {
                    setDeleteModalOpen(false);
                    handleDelete();
                }}
            />

            {/*   */}
            <h1 className={styles.reviewtitle}>{review.rtitle || " "}</h1>
            <div className={styles.reviewInfo}>
                <p>: {review.rwriter || "  "}</p>
                <p>: {review.rwdate || " "}</p>
                <p>: {review.rcount || 0}</p>
            </div>
            <div className={styles.reviewcontent}>{review.rcontent || " "}</div>

            {/*    */}
            {review.reviewPath && (
                <div className={styles.fileContainer}>
                    <span className={styles.fileIcon}>
                        <img src={downloadIcon} alt=" " />
                    </span>
                    {/*    */}
                    <a
                        href={`http://localhost:8080/review/attachments/${review.reviewPath}`}
                        target="_blank" //   
                        rel="noopener noreferrer"
                        className={styles.previewLink}
                    >
                         
                    </a>
                </div>
            )}

            {filePreview && isImageFile(review.reviewPath) && (
                <div className={styles.reviewImageContainer}>
                    <img
                        src={filePreview}
                        alt=""
                        className={styles.reviewImage}
                    />
                    <button
                        onClick={handleDownload}
                        className={styles.downloadButton}
                    >
                         
                    </button>
                </div>
            )}

            {/*     */}
            <div className={styles.buttonGroup}>
                <button
                    onClick={() => navigate(`/review/update/${rno}`)}
                    className={styles.editButton}
                >
                    
                </button>
                <button
                    onClick={() => setDeleteModalOpen(true)}
                    className={styles.deleteButton}
                >
                    
                </button>
            </div>

            {/*   */}
            <button onClick={handleBack} className={styles.backButton}>
                
            </button>
        </div>
    );
    
}

export default ReviewDetail;
