import React, { useEffect, useState, useContext } from "react";
import { useLocation, useNavigate } from "react-router-dom"; // useLocation 
import { AuthContext } from "../../AuthProvider";
import styles from "./AdminMemberManagementDetail.module.css";
import BackButton from "../../components/common/button/BackButton";
import RestricationModal from "./RestricationModal";

function AdminMemberManagementDetail() {
  const location = useLocation(); // state   useLocation 
  const navigate = useNavigate();
  const { secureApiRequest } = useContext(AuthContext);
  const [member, setMember] = useState(null);

  const [isModalOpen, setIsModalOpen] = useState(false);

  const openModal = () => setIsModalOpen(true); //  
  const closeModal = () => setIsModalOpen(false); //  

  const [loading, setLoading] = useState(false);

  // state uuid   
  const { uuid, page } = location.state;

  console.log("  : ", page)
  //   
  const goBack = () => {
    navigate("/adminMemberManagement", { state: { page } });
  };
  

  const formatDateTime = (dateTime) => {
    if (!dateTime) return "";
    const date = new Date(dateTime);
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, "0");
    const day = String(date.getDate()).padStart(2, "0");
    const hours = String(date.getHours()).padStart(2, "0");
    const minutes = String(date.getMinutes()).padStart(2, "0");
    const seconds = String(date.getSeconds()).padStart(2, "0");
    return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
  };

  const formatPhoneNumber = (phoneNumber) => {
    if (!phoneNumber) return ""; //      
    return phoneNumber.replace(/(\d{3})(\d{4})(\d{4})/, "$1-$2-$3");
  };

  useEffect(() => {
    const fetchMemberDetail = async () => {
      try {
        if (!uuid) {
          alert("  .");
          return; //  navigate(-1)       
        }
  
        setLoading(true); //   
        const response = await secureApiRequest(
          `/admin/memberDetail?uuid=${uuid}`
        );
        setMember(response.data);
        setLoading(false); //   
      } catch (error) {
        console.error("     :", error.message);
        alert("    .");
      }
    };
  
    fetchMemberDetail();
  }, [uuid, secureApiRequest]); // navigate 

  const handleLiftSanction = async () => {
    if (!window.confirm(`${member.userName}   ?`)) {
      return; //  ''   
    }

    try {
      await secureApiRequest("/admin/memberLiftSanction", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          uuid: member.uuid, //  UUID
        }),
      });

      alert("   .");
      navigate("/adminMemberDetail");
    } catch (error) {
      console.error("     :", error);
      alert("   .");
    }
  };

  const handleUnsubscribeLift = async () => {
    if (
      !window.confirm(`${member.userName}    ?`)
    ) {
      return; //  ''   
    }

    try {
      await secureApiRequest("/admin/memberUnsubscribeLift", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          uuid: member.uuid, //  UUID
        }),
      });

      alert("    .");
      navigate("/adminMemberDetail");
    } catch (error) {
      console.error("     :", error);
      alert("   .");
    }
  };

  const handleDeleteMember = async () => {
    if (!window.confirm("   ?")) {
      return; //  ''   
    }

    try {
      const response = await secureApiRequest("/admin/deleteMember", {
        method: "DELETE",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          uuid: member.uuid,
        }),
      });
      // alert(response); //  
      alert("   .");
      navigate("/adminMemberManagement");
    } catch (error) {
      console.error("     :", error.message);
      alert("   .");
    }
  };

  const promoteToAdmin = async () => {
    if (
      !window.confirm(
        ` ${member.userName}   ?`
      )
    ) {
      return;
    }
    setLoading(true);
    try {
      await secureApiRequest(`/admin/promoteToAdmin`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          uuid: member.uuid,
        }),
      });

      setMember({ ...member, adminYn: "Y" });
      alert(" .");
      navigate("/adminMemberDetail");
    } catch (error) {
      console.error("    :", error);
      alert(`   : ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  const demoteToUser = async () => {
    if (
      !window.confirm(
        ` ${member.userName}    ?`
      )
    ) {
      return;
    }
    setLoading(true);
    try {
      await secureApiRequest(`/admin/demoteToUser`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          uuid: member.uuid,
        }),
      });

      setMember({ ...member, adminYn: "N" });
      alert("  .");
      navigate("/adminMemberDetail");
    } catch (error) {
      console.error("    :", error);
      alert(`    : ${error.message}`);
    } finally {
      setLoading(false);
    }
  };

  if (!member) {
    return <p>  ...</p>;
  }

  return (
    <div className={styles.container}>
      <h2>  </h2>
      <table className={styles.table}>
        <tbody>
          <tr>
            <td className={styles.title}></td>
            <td>{member.userName}</td>
            <td className={styles.title}> </td>
            <td>{member.userId}</td>
          </tr>
          <tr>
            <td className={styles.title}></td>
            <td>{member.userDefaultEmail}</td>
            <td className={styles.title}> </td>
            <td> {member.adminYn === "Y" ? "" : ""}</td>
          </tr>
          <tr>
            <td className={styles.title}> </td>
            <td>{formatPhoneNumber(member.userPhone)}</td>
            <td className={styles.title}> </td>
            <td></td>
          </tr>
          <tr>
            <td className={styles.title}></td>
            <td>{formatDateTime(member.userCreateAt)}</td>
            <td className={styles.title}>  </td>
            <td>{formatDateTime(member.userUpdateAt)}</td>
          </tr>
          <tr>
            <td className={styles.title}></td>
            <td>{member.userBirthday}</td>
            <td className={styles.title}></td>
            <td>{member.userGender === "M" ? "" : ""}</td>
          </tr>

          <tr>
            <td className={styles.title}></td>
            <td></td>
            <td className={styles.title}> </td>
            <td></td>
          </tr>

          <tr>
            <td className={styles.title}> </td>
            <td>{member.userKakaoEmail ? member.userKakaoEmail : ""}</td>
            <td className={styles.title}> </td>
            <td>{member.userNaverEmail ? member.userNaverEmail : ""}</td>
          </tr>
          <tr>
            <td className={styles.title}> </td>
            <td>{member.userGoogleEmail ? member.userGoogleEmail : ""}</td>
            <td className={styles.title}>  </td>
            <td>{member.userFaceIdStatus === "Y" ? "" : ""}</td>
          </tr>
          <tr>
            <td className={styles.title}> </td>
            <td>{member.userRestrictionStatus === "Y" ? "" : ""}</td>
            <td className={styles.title}> </td>
            <td>{member.userRestrictionReason || ""}</td>
          </tr>
          <tr>
            <td className={styles.title}> </td>
            <td>{member.userDeletionStatus === "Y" ? "" : ""}</td>
            <td className={styles.title}> </td>
            <td>{member.userDeletionReason || ""}</td>
          </tr>
        </tbody>
      </table>
      <div className={styles.btnContainer}>
        <BackButton onClick={goBack} />
        {member.userRestrictionStatus === "Y" ? (
          <button
            className={styles.sanctionsLiftedBtn}
            onClick={handleLiftSanction}
          >
             
          </button>
        ) : (
          <button className={styles.restricationBtn} onClick={openModal}>
             
          </button>
        )}
        {member.userDeletionStatus === "Y" && (
          <>
            <button
              className={styles.unsubscribeBtn}
              onClick={handleUnsubscribeLift}
            >
               
            </button>
            <button className={styles.deleteBtn} onClick={handleDeleteMember}>
               
            </button>
          </>
        )}
        {member.adminYn === "Y" ? (
          <button className={styles.changeUserBtn} onClick={demoteToUser}>
             
          </button>
        ) : (
          <button className={styles.changeAdminBtn} onClick={promoteToAdmin}>
             
          </button>
        )}
      </div>

      {isModalOpen && (
        <RestricationModal onClose={closeModal} memberUuid={uuid} />
      )}
    </div>
  );
}

export default AdminMemberManagementDetail;
