import { useParams, useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";
import { useContext } from "react";
import { AuthContext } from "../../AuthProvider";
import styles from "./MyIntroductionDetail.module.css";
import MypageSubMenubar from "../../components/common/subMenubar/MypageSubMenubar";

function MyIntroductionDetail() {
  const { id } = useParams(); // URL id 
  const navigate = useNavigate();
  const { secureApiRequest } = useContext(AuthContext);
  const [detail, setDetail] = useState(null); //   
  const [loading, setLoading] = useState(true); //  
  const [error, setError] = useState(null); //  
  const [isModalOpen, setIsModalOpen] = useState(false);

  useEffect(() => {
    const fetchDetail = async () => {
      try {
        console.log("   . ID:", id);
        const response = await secureApiRequest(`/mypage/intro/detail/${id}`, {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
          },
        });
        const data = response.data; // axios   
        console.log("  :", data);
        setDetail(data);
      } catch (err) {
        console.error(
          "      :",
          err.message
        );
        setError("    .");
      } finally {
        setLoading(false);
      }
    };

    fetchDetail();
  }, [id, secureApiRequest]);

  const openDeleteModal = () => {
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };

  const confirmWithdrawal = async () => {
    try {
      const response = await secureApiRequest(`/mypage/intro/delete/${id}`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          introIsDeleted: "Y",
          introDeletedDate: new Date().toISOString(),
        }),
      });

      if (response.status === 200) {
        alert(" .");
        navigate("/myIntroductionList");
      } else {
        alert(" .");
      }
    } catch (err) {
      console.error("   :", err.message);
    } finally {
      closeModal();
    }
  };

  if (loading) return <div> ...</div>;
  if (error) return <div className={styles.error}>{error}</div>;
  if (!detail) return <div> .</div>;

  return (
    <div>
      <MypageSubMenubar />
      <div className={styles.container}>
        <h1 className={styles.title}></h1>
        <div className={styles.headerRow}>
          <h2
            className={styles.subtitle}
          >{`${detail.introTitle}`}</h2>
          <div className={styles.buttons}>
            <button
              className={styles.listButton}
              onClick={() => navigate("/myIntroductionList")}
            >
              
            </button>
            <button
              className={styles.editButton}
              onClick={() => navigate(`/MyIntroductionUpdate/${id}`)}
            >
              
            </button>
            <button className={styles.deleteButton} onClick={openDeleteModal}></button>
          </div>
        </div>
        <div className={styles.maincontainer}>
          <div className={styles.rowContainer}>
            <div className={styles.infoBox}>
              <strong> :</strong>
              <div className={styles.content}>
                {detail.applicationCompanyName}
              </div>
            </div>
            <div className={styles.infoBox}>
              <strong> :</strong>
              <div className={styles.content}>{detail.workType}</div>
            </div>
            <div className={styles.infoBox}>
              <strong> :</strong>
              <div className={styles.content}>{detail.certificate}</div>
            </div>
          </div>
          <div className={styles.line}></div>
          <div className={styles.contentWrapper}>
            <strong>:</strong>
            <div className={styles.bigcontent}>{detail.introContents}</div>
          </div>
        </div>
      </div>
      {isModalOpen && (
        <div className={styles.modal}>
          <div className={styles.modalContent}>
            <h2> </h2>
            <p> <span>{detail.introTitle}</span><br />  ?</p>
            <button
              className={styles.confirmButton}
              onClick={confirmWithdrawal}
            >
              
            </button>
            <button className={styles.cancelButton} onClick={closeModal}>
              
            </button>
          </div>
        </div>
      )}
    </div>
  );
}

export default MyIntroductionDetail;
