import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import apiClient from "../../utils/axios";
import styles from './CancelList.module.css';



function CancelList (){

    const tickets = [
       { name: "(6) 5+1 ", price: "47000 ", date: "2024.11.01" },
       { name: "(24) 1 ", price: "9900 ", date: "2024.10.28" },
       { name: "(24) 1 ", price: "9900 ", date: "2024.10.28" },
     ];


    return(
    <div className={styles.container}>
        <h1 className={styles.header}> </h1>
        <table className={styles.table}>
            <thead>
                <tr>
                    <th className={styles.tTName}> </th>
                    <th className={styles.tTh}></th>
                    <th className={styles.tTh}> </th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
            {tickets.map((ticket, index) => (
                <tr key={index}>
                    <td className={styles.tName}>{ticket.name}</td>
                    <td className={styles.tPrice}>{ticket.price}</td>
                    <td className={styles.tDate}>{ticket.date}</td>
                </tr>
            ))}
            </tbody>
        </table>
        <button className={styles.cancelButton}> </button>
    </div>
    );
}

export default CancelList;