import React, { useState, useContext } from "react";
import { AuthContext } from "../../AuthProvider"; // AuthProvider 
import styles from "./RestricationModal.module.css";
import { useNavigate } from "react-router-dom";

function RestricationModal({ onClose, memberUuid }) {
  const { secureApiRequest } = useContext(AuthContext); // secureApiRequest 
  const [reason, setReason] = useState(""); //   
  const [customReason, setCustomReason] = useState(""); //  
  const [isInputDisabled, setIsInputDisabled] = useState(false); // input   

  const navigate = useNavigate(); // useNavigate  

  const handleSelectChange = (e) => {
    const selectedReason = e.target.value;
    setReason(selectedReason);

    if (selectedReason === "") {
      setIsInputDisabled(false); //   
      setCustomReason(""); //   
    } else {
      setIsInputDisabled(true); //   
      setCustomReason(selectedReason); //     
    }
  };

  const handleSubmit = async () => {
    const finalReason = isInputDisabled ? reason : customReason; //   
    console.log("  ");
    console.log(memberUuid);
    console.log(finalReason);

    if (!finalReason) {
      alert("  .");
      return;
    }

    try {
      // secureApiRequest    
      await secureApiRequest("/admin/memberRestrict", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          uuid: memberUuid, // uuid 
          userRestrictionReason: finalReason, //  
        }),
      });

      alert("  .");
      onClose(); //  
      navigate("/adminMemberDetail");
    } catch (error) {
      console.error("    :", error);
      alert("  .");
    }
  };

  return (
    <div className={styles.modalOverlay}>
      <div className={styles.modalContent}>
        <h2 className={styles.title}>  </h2>
        <div className={styles.reason}> </div>
        <div className={styles.reasonContainer}>
          <select onChange={handleSelectChange} className={styles.select}>
            <option value="" disabled selected>
              
            </option>
            <option value="  ">  </option>
            <option value=" "> </option>
            <option value=""></option>
          </select>
          <input
            type="text"
            value={customReason}
            onChange={(e) => setCustomReason(e.target.value)}
            className={styles.input}
            placeholder=" "
            disabled={isInputDisabled}
          />
        </div>
        <div className={styles.btnContainer}>
          <button onClick={onClose} className={styles.closeBtn}>
            
          </button>
          <button onClick={handleSubmit} className={styles.restricationBtn}>
            
          </button>
        </div>
      </div>
    </div>
  );
}

export default RestricationModal;
