import React, { useEffect, useState, useContext } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { AuthContext } from "../../AuthProvider";
import styles from "./QnaDetail.module.css";
import DeleteModal from "../../components/common/DeleteModal";
import axios from "axios";

function QnaDetail() {
  const { qno } = useParams(); // URL QnA  
  const navigate = useNavigate();
  const [qna, setQna] = useState(null); // QnA  
  const [replies, setReplies] = useState([]); //   
  const [error, setError] = useState(null); //   
  const [isDeleteModalOpen, setDeleteModalOpen] = useState(false); //   
  const [newReply, setNewReply] = useState(""); //   
  const [isReplySubmitting, setReplySubmitting] = useState(false); //    
  const [filePreview, setFilePreview] = useState(null); //   

  const { isLoggedIn, userid, username, role, secureApiRequest } =
    useContext(AuthContext);

  // QnA    
  const fetchQnaDetail = async () => {
    try {
      const response = await secureApiRequest(`/qna/detail/${qno}`, {
        method: "GET",
      });

      if (response.data?.qna) {
        setQna(response.data.qna);
        setReplies(response.data.replies || []);

        //   
        if (response.data.qna.qattachmentTitle) {
          const previewUrl = await fetchImage(
            `/qna/attachments/${response.data.qna.qattachmentTitle}`
          );
          setFilePreview(previewUrl);
        }
      } else {
        setError("  .");
      }
    } catch (error) {
      console.error("API  :", error);
      if (error.response?.status === 404) {
        setError("  .");
      } else {
        setError("  .");
      }
    }
  };

  useEffect(() => {
    if (isLoggedIn) {
      fetchQnaDetail(); //    
    } else {
      setError(" .");
    }
  }, [qno, isLoggedIn]);

  //  URL  
  const fetchImage = async (url) => {
    try {
      const response = await fetch(`http://localhost:8080${url}`);
      if (!response.ok) throw new Error("  ");
      const blob = await response.blob();
      return URL.createObjectURL(blob);
    } catch (error) {
      console.error("  :", error);
      return null;
    }
  };

  // QnA   
  const handleDelete = async () => {
    try {
      await secureApiRequest(`/qna/${qno}/delete`, {
        method: "PUT",
      });
      alert(" .");
      navigate(-1);
    } catch (error) {
      console.error("Delete error:", error);
      alert(" !");
    }
  };

  //    
  const handleReplyDelete = async (repno) => {
    if (!repno) {
      alert("   .");
      return;
    }

    if (window.confirm(" ?")) {
      try {
        await secureApiRequest(`/reply/${repno}/delete`, {
          method: "PUT",
        });
        alert(" .");
        //   
        fetchQnaDetail(); //     
      } catch (error) {
        console.error("Reply delete error:", error);
        alert("  !");
      }
      fetchQnaDetail(); //     
    }
  };

  //    
  const handleReplySubmit = async () => {
    if (!newReply.trim()) {
      alert("  .");
      return;
    }

    const accessToken = localStorage.getItem("accessToken");
    const refreshToken = localStorage.getItem("refreshToken");
    const uuid = localStorage.getItem("uuid");


    const requestData = {
      replyContent: newReply,
      uuid,
      replyWriter: username,
    };

    setReplySubmitting(true);
    try {
      await axios.post(`http://localhost:8080/reply/${qno}`, requestData, {
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${accessToken}`,
          refreshToken: `Bearer ${refreshToken}`,
        },
      });
      alert(" .");
      setNewReply("");
      fetchQnaDetail(); //     
    } catch (error) {
      console.error("  :", error);
      alert("  .");
    } finally {
      setReplySubmitting(false);
    }
  };

  const handleReplyChange = (e) => {
    const value = e.target.value;
    if (value.length > 100) {
      alert("  100  .");
      return;
    }
    setNewReply(value);
  };

  if (!qna && !error) {
    return <div className={styles.loading}> ...</div>;
  }

  if (error) {
    return <div className={styles.error}>{error}</div>;
  }

  return (
    <div className={styles.qnaDetailContainer}>
      <DeleteModal
        isOpen={isDeleteModalOpen}
        onClose={() => setDeleteModalOpen(false)}
        onConfirm={() => {
          setDeleteModalOpen(false);
          handleDelete();
        }}
      />
      <h1 className={styles.qnaTitle}>{qna.qtitle}</h1>
      <div className={styles.buttonGroup}>
    <button onClick={() => navigate(-1)} className={styles.backButton}>
          
    </button>
    {isLoggedIn && (username === qna.qwriter || role === "ADMIN") && (
        <>
            <button
                onClick={() => navigate(`/qna/update/${qno}`)}
                className={styles.editButton}
            >
                
            </button>
            <button
                onClick={() => setDeleteModalOpen(true)}
                className={styles.deleteButton}
            >
                
            </button>
        </>
    )}
    {/*   */}
    <div className={styles.divider}></div>
</div>


      <table className={styles.qnaTable}>
        <tbody>
          <tr>
            <th></th>
            <td>
              {/*   */}
              {filePreview && (
                <div className={styles.attachmentPreview}>
                  <img
                    src={filePreview}
                    alt=" "
                    className={styles.previewImage}
                    style={{
                      maxWidth: "100%",
                      maxHeight: "300px",
                      marginBottom: "10px",
                    }}
                  />
                </div>
              )}
              {/* QnA  */}
              <div className={styles.qnaContent}>{qna.qcontent}</div>
            </td>
          </tr>
        </tbody>
      </table>

      <div>
        <h3></h3>
        {replies.length > 0 ? (
          <table className={styles.commentsTable}>
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th></th>
                {role === "ADMIN" && <th></th>}
              </tr>
            </thead>
            <tbody>
              {replies
                .filter((reply) => reply.repisdeleted !== "Y") //    
                .map((reply) => (
                  <tr key={reply.repno}>
                    <td>{reply.repwriter || " "}</td>
                    <td>{reply.repcontent || " "}</td>
                    <td>
                      {new Date(reply.repdate).toLocaleString() || " "}
                    </td>
                    {(role === "ADMIN" || username === reply.repwriter) && (
                      <td>
                        <button
                          onClick={() => handleReplyDelete(reply.repno)}
                          className={styles.deleteButton}
                        >
                          
                        </button>
                      </td>
                    )}
                  </tr>
                ))}
            </tbody>
          </table>
        ) : (
          <div className={styles.noComments}> .</div>
        )}
        {role === "ADMIN" && (
          <div className={styles.replyForm}>
            <textarea
              placeholder="  ( 100)"
              value={newReply}
              onChange={handleReplyChange}
            />
            <button onClick={handleReplySubmit} disabled={isReplySubmitting}>
               
            </button>
          </div>
        )}
      </div>
    </div>
  );
}

export default QnaDetail;
