import React, { useEffect, useState, useContext } from "react";
import { AuthContext } from "../../AuthProvider";
import MypageSubMenubar from "../../components/common/subMenubar/MypageSubMenubar";
import styles from "./DeleteUser.module.css";

function DeleteUser({ memberUuid }) {
  const { secureApiRequest } = useContext(AuthContext);
  const [userDeletionReason, setDeletionReason] = useState(""); //  
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [userId, setUserId] = useState(null); //    userId

  useEffect(() => {
    const storedUserId = localStorage.getItem("userId");
    const accessToken = localStorage.getItem("accessToken");
    const refreshToken = localStorage.getItem("refreshToken");
    const userName = localStorage.getItem("userName");
    const role = localStorage.getItem("role");
    if (storedUserId) {
      setUserId(storedUserId);
    } else {
      console.error("  userId .");
    }
  }, [secureApiRequest]);

  // const handleFeedbackChange = (event) => {
  //   console.log("Feedback:", event.target.value);
  // };

  const handleSecession = async () => {
    console.log("   .");
    console.log("  userId:", userId);
    console.log(" :", userDeletionReason);
  
    if (!userId) {
      alert("  .  .");
      return;
    }
  
    try {
      const response = await secureApiRequest(`/mypage/secession/${userId}`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          userDeletionStatus: "Y",
          userDeletionReason: userDeletionReason || null,
        }),
      });
  
      if (response.status >= 200 && response.status < 300) {
        console.log("   .");
        alert("  .");
        localStorage.clear();
        window.location.href = "/";
      } else {
        console.error(`   : ${response.status}`);
        alert("     .");
      }
    } catch (error) {
      console.error("API   :", error);
      alert("   .  .");
    }
  };

  const openModal = () => {
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  };

  const confirmWithdrawal = () => {
    setIsModalOpen(false);
    handleSecession();
  };
  return (
    <div>
      <MypageSubMenubar />
      <div className={styles.maincontainer}>
        <h1 className={styles.menutitle}></h1>
        <div className={styles.container}>
          <h1 className={styles.title}>JOBIS</h1>
          <p className={styles.minititle}> </p>
          <p className={styles.warning}>
                 . ,   7
             ,      . ,
                 ,   
            ,    .    
               .
          </p>
          <textarea
            className={styles.textarea}
            placeholder="*            ."
            onChange={(e) => setDeletionReason(e.target.value)}
          />
          <button className={styles.button} onClick={openModal}>
             
          </button>
        </div>
      </div>
      {isModalOpen && (
        <div className={styles.modal}>
          <div className={styles.modalContent}>
            <h2> </h2>
            <p>  ?</p>
            <p>   .</p>
            <button
              className={styles.confirmButton}
              onClick={confirmWithdrawal}
            >
              
            </button>
            <button className={styles.cancelButton} onClick={closeModal}>
              
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
export default DeleteUser;
