import React from "react";
import styles from "./DeleteModal.module.css"; // CSS 

function DeleteModal({ isOpen, onClose, onConfirm }) {
  if (!isOpen) return null; //      

  return (
    <div className={styles.modalOverlay}> {/*   */}
      <div className={styles.modalContent}> {/*   */}
        <h2 className={styles.modalTitle}> </h2>
        <p className={styles.modalMessage}> ?     .</p>
        <div className={styles.buttonGroup}> {/*   */}
          <button onClick={onConfirm} className={styles.confirmButton}></button>
          <button onClick={onClose} className={styles.cancelButton}></button>
        </div>
      </div>
    </div>
  );
}

export default DeleteModal;
