import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { useContext } from "react";
import { AuthContext } from "../../AuthProvider"; // AuthContext 
import styles from "./JobPostingSearch.module.css";
import JobPostingSubMenubar from "../../components/common/subMenubar/JobPostingSubMenubar";

//   
const filterOptions = {
  job_cd: [
    { value: "", label: " " },
    { value: "16", label: "" },
    { value: "14", label: "" },
    { value: "3", label: "" },
    { value: "5", label: "HRD" },
    { value: "4", label: "" },
    { value: "2", label: "IT" },
    { value: "15", label: "" },
    { value: "8", label: "" },
    { value: "21", label: "TM" },
    { value: "18", label: "" },
    { value: "12", label: "MD" },
    { value: "7", label: "" },
    { value: "10", label: "" },
    { value: "11", label: "" },
    { value: "22", label: "" },
    { value: "6", label: "" },
    { value: "9", label: "R&D" },
    { value: "19", label: "" },
    { value: "13", label: "" },
    { value: "17", label: "" },
    { value: "20", label: "" },
  ],
  loc_mcd: [
    { value: "", label: " " },
    { value: "117000", label: "" },
    { value: "101000", label: "" },
    { value: "102000", label: "" },
    { value: "103000", label: "" },
    { value: "104000", label: "" },
    { value: "105000", label: "" },
    { value: "106000", label: "" },
    { value: "107000", label: "" },
    { value: "108000", label: "" },
    { value: "109000", label: "" },
    { value: "110000", label: "" },
    { value: "111000", label: "" },
    { value: "112000", label: "" },
    { value: "113000", label: "" },
    { value: "114000", label: "" },
    { value: "115000", label: "" },
    { value: "116000", label: "" },
    { value: "118000", label: "" },
  ],
  edu_lv: [
    { value: "", label: " " },
    { value: "0", label: "" },
    { value: "1", label: "" },
    { value: "2", label: "(2,3)" },
    { value: "3", label: "(4)" },
    { value: "4", label: "" },
    { value: "5", label: "" },
    { value: "6", label: "" },
    { value: "7", label: "(2,3)" },
    { value: "8", label: "(4)" },
    { value: "9", label: "" },
  ],
  job_type: [
    { value: "", label: " " },
    { value: "1", label: "" },
    { value: "2", label: "" },
    { value: "4", label: "" },
    { value: "5", label: "" },
    { value: "10", label: " ( )" },
    { value: "11", label: " ( )" },
    { value: "16", label: "" },
  ],
 
};

const JobPostingSearch = () => {
  const { secureApiRequest } = useContext(AuthContext);
  const initialFilters = {
    job_cd: "",
    loc_mcd: "",
    edu_lv: "",
    job_type: "",
  };
  const [filters, setFilters] = useState(initialFilters);
  const [loading, setLoading] = useState(false);  //   
  const [error, setError] = useState(null); //   
  const navigate = useNavigate();

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFilters((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  useEffect(() => {
    const accessToken = localStorage.getItem("accessToken");
    if (!accessToken) {
      alert(" .");
      navigate("/login");
    }
  }, [navigate]);

  const handleSearch = async () => {
    setLoading(true);  //      
    setError(null); //  
    try {
      const queryParams = new URLSearchParams();

      //       
      if (filters.job_cd) queryParams.append('job_cd', filters.job_cd);
      if (filters.loc_mcd) queryParams.append('loc_cd', filters.loc_mcd);
      if (filters.edu_lv) queryParams.append('edu_lv', filters.edu_lv);
      if (filters.job_type) queryParams.append('job_type', filters.job_type);

      const response = await secureApiRequest(`/jobposting/search?${queryParams}`, {
        method: "GET",
      });

      if (response.data) {
        navigate("/jobPosting/search", {
          state: { jobPosting: response.data.jobPostings, filters },
        });
      }
    } catch (error) {
      console.error("    :", error);
      setError("    .");
    } finally {
      setLoading(false);  //      
    }
  };

  const resetFilters = () => {
    setFilters(initialFilters);
  };

  return (
    <div>
      <JobPostingSubMenubar />
      <div className={styles.container}>
        <h2> </h2>
        <div className={styles.filters}>
          {Object.keys(filterOptions).map((filterKey) => (
            <select
              key={filterKey}
              name={filterKey}
              value={filters[filterKey]}
              onChange={handleInputChange}
              className={styles.select}
            >
              {filterOptions[filterKey].map((option) => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </select>
          ))}
          <button onClick={handleSearch} className={styles.searchButton} disabled={loading}>
            {loading ? " ..." : ""}
          </button>
          <button onClick={resetFilters} className={styles.resetButton}>
            
          </button>
        </div>
        {error && <div className={styles.error}>{error}</div>} {/*    */}
      </div>
    </div>
  );
};

export default JobPostingSearch;
