import React, { useEffect, useState, useContext } from 'react';
import { useParams } from 'react-router-dom';
import { AuthContext } from "../../AuthProvider"; // AuthContext 
import { useNavigate } from "react-router-dom";
import apiClient from "../../utils/axios";
import styles from './TicketInfo.module.css';



function TicketInfo (){
    const { isLoggedIn, isAuthInitialized, secureApiRequest, role } = useContext(AuthContext);
    const [ticketList, setTicketList] = useState([]); //   
    const [totalItems, setTotalItems] = useState(0); //    
    const [currentPage, setCurrentPage] = useState(1); //   
    const [isLoading, setIsLoading] = useState(false); //  
    const [error, setError] = useState(null); //   

    const navigate = useNavigate();

    const fetchTicketList = async (page = 1) => {
        setIsLoading(true);
        setError(null);
      
        try {
          const response = await secureApiRequest(`/ticket?page=${page}&size=10`, {
            method: "GET",
          });
      
          //    
          console.log("API  :", response.data);
      
          const filteredList = response.data.list.filter(); // 
          setTicketList(filteredList);
          setTotalItems(response.data.paging.totalItems);
          setCurrentPage(page);
        } catch (err) {
          console.error("   :", err);
          setError("    .");
        } finally {
          setIsLoading(false);
        }
      };

       useEffect(() => {
          if (!isAuthInitialized && !isLoggedIn) {
            console.log("  .   .");
            navigate("/login");
          } else {
            fetchTicketList(currentPage); //     
          }
        }, [isLoggedIn, isAuthInitialized, navigate]);

    const tickets = [
       { name: "(6) 5+1 ", price: "47000 ", date: "2024.11.01", cancelable: true },
       { name: "(24) 1 ", price: "9900 ", date: "2024.10.28", cancelable: false },
       { name: "(24) 1 ", price: "9900 ", date: "2024.10.28", cancelable: false },
     ];


    return(
    <div className={styles.container}>
        <h1 className={styles.header}> </h1>
        <table className={styles.table}>
            <thead>
            <tr>
                <th className={styles.tTName}> </th>
                <th className={styles.tTh}></th>
                <th className={styles.tTh}> </th>
                <th></th>
            </tr>
            </thead>
            <tbody>
            {tickets.map((ticket, index) => (
                <tr key={index}>
                <td className={styles.tName}>{ticket.name}</td>
                <td className={styles.tPrice}>{ticket.price}</td>
                <td className={styles.tDate}>{ticket.date}</td>
                <td>{ticket.cancelable && (
                        <span><button className={styles.cancelButton}></button></span>
                    )}</td>
                </tr>
            ))}
            </tbody>
        </table>
    </div>
    );
}

export default TicketInfo;