import React, { useState, useEffect, useRef, useContext } from "react";
import style from "./Signup.module.css";
import { useNavigate, useLocation } from "react-router-dom";
import apiClient from "../../utils/axios";
import kakaoLogo from "../../assets/images/kakaotalk.png";
import naverLogo from "../../assets/images/naver.png";
import googleLogo from "../../assets/images/google.png";

function SNSSignup() {
  const location = useLocation();
  const initialEmail = location.state?.email || ""; //   
  const snsType = location.state?.snsType || ""; // snsType  

  

  const [isPhoneAvailable, setIsPhoneAvailable] = useState(false); //     
  const [phoneMessage, setPhoneMessage] = useState(""); //  

  const navigate = useNavigate(); //  navigate 
  const [userId, setUserId] = useState("");
  const [isIdAvailable, setIsIdAvailable] = useState(false);
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [confirmPasswordError, setConfirmPasswordError] = useState("");
  const [isPasswordValid, setIsPasswordValid] = useState(true); //   
  const passwordInputRef = useRef(null);
  const [birthDate, setBirthDate] = useState("");
  const [birthDateError, setBirthDateError] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [phoneError, setPhoneError] = useState(""); 
  const [allChecked, setAllChecked] = useState(false); //   
  const [serviceAgreement, setServiceAgreement] = useState(false); //    
  const [privacyPolicy, setPrivacyPolicy] = useState(false); //      
  const [marketingConsent, setMarketingConsent] = useState(false); //     
  const [name, setName] = useState(""); //   
  const [gender, setGender] = useState("male"); //   (: )

  const handleAllCheck = (e) => {
    const checked = e.target.checked;

    //    
    setAllChecked(checked);
    setServiceAgreement(checked);
    setPrivacyPolicy(checked);
    setMarketingConsent(checked);
  };

  const handleIndividualCheck = (type, checked) => {
    if (type === "service") {
      setServiceAgreement(checked);
    } else if (type === "privacy") {
      setPrivacyPolicy(checked);
    } else if (type === "marketing") {
      setMarketingConsent(checked);
    }

    //     (     true)
    setAllChecked(
      (type === "service" ? checked : serviceAgreement) &&
        (type === "privacy" ? checked : privacyPolicy) &&
        (type === "marketing" ? checked : marketingConsent)
    );
  };

  const handleUserIdChange = (e) => {
    setUserId(e.target.value);
    setIsIdAvailable(false);
  };

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
    setIsPasswordValid(true); //      
  };

  const handleConfirmPasswordChange = (e) => {
    setConfirmPassword(e.target.value);
  };

  const validatePassword = () => {
    const passwordRegex =
      /^(?=.*[A-Za-z])(?=.*[\d@$!%*?&])[A-Za-z\d@$!%*?&]{8,16}$/;

    //      `alert` 
    if (!passwordRegex.test(password)) {
      if (isPasswordValid) {
        //      alert  
        alert(
          " 8~16,  , ,   2   ."
        );
      }
      setPassword(""); //  
      setIsPasswordValid(false); //  
      passwordInputRef.current.focus(); //  
    } else {
      setIsPasswordValid(true); //     
    }
  };

  useEffect(() => {
    if (confirmPassword) {
      if (password !== confirmPassword) {
        setConfirmPasswordError(" .");
      } else {
        setConfirmPasswordError("");
      }
    }
  }, [password, confirmPassword]);

  const handleBirthDateChange = (e) => {
    const value = e.target.value;

    if (/^\d*$/.test(value)) {
      setBirthDate(value);

      if (value.length !== 8) {
        setBirthDateError("* 8 ");
      } else {
        setBirthDateError("");
      }
    }
  };

  const handlePhoneNumberChange = (e) => {
    const value = e.target.value;

    if (/^\d*$/.test(value)) {
      setPhoneNumber(value);
      setPhoneError("");
    } else {
      setPhoneError("*   ");
    }
  };

  const handlePhoneCheck = async () => {
    if (phoneNumber.length === 0) {
      setPhoneError("* ");
      return;
    }

    try {
      const response = await apiClient.post("/users/checkPhoneNumber", {
        phoneNumber,
      });

      if (response.data === "dup") {
        alert("   .");
        setPhoneMessage(""); //    
        setIsPhoneAvailable(false);
      } else {
        alert("  .");
        setPhoneMessage("*  .");
        setIsPhoneAvailable(true);
      }
    } catch (error) {
      console.error("    :", error);
      alert("    .");
    }
  };

  const checkDuplicateId = async () => {
    if (!userId) {
      alert(" .");
      return;
    }

    if (!/^[a-z0-9]{6,20}$/.test(userId)) {
      alert(" 6~20,      .");
      return;
    }

    try {
      const response = await apiClient.post("/users/checkuserId", {
        users: userId,
      });

      if (response.data === "dup") {
        alert("   .");
        setIsIdAvailable(false);
      } else {
        alert("  .");
        setIsIdAvailable(true);
      }
    } catch (error) {
      console.error("    :", error.message);
      alert("    .");
    }
  };

  const handleSignup = async () => {
    //  
    if (!isIdAvailable) {
      alert("  .");
      return;
    }
    if (!isPasswordValid || confirmPasswordError) {
      alert(" .");
      return;
    }
   
    if (!birthDate || birthDateError) {
      alert("  .");
      return;
    }
    if (!phoneNumber || phoneError) {
      alert(" .");
      return;
    }
    if (!serviceAgreement || !privacyPolicy) {
      alert("  .");
      return;
    }

    //   
    //   
    const formattedBirthDate = `${birthDate.slice(0, 4)}-${birthDate.slice(
      4,
      6
    )}-${birthDate.slice(6, 8)}`;
    // gender  "M"  "F" 
    const genderValue = gender === "male" ? "M" : "F";
    const signupData = {
        user: {
            userId: userId,
            userPw: password,
            userDefaultEmail: initialEmail,
            userName: name,
            userBirthday: formattedBirthDate,
            userPhone: phoneNumber,
            userGender: genderValue,
        },
        snsType: snsType, //  
    };

    //     
    console.log(" :", signupData);

    try {
      const response = await apiClient.post(
        "/users/snsSignup",
        signupData
      );

      if (response.data === "success") {
        alert(" .");
        //      
        navigate("/login"); // '/login' 
      } else {
        alert(" .  .");
      }
    } catch (error) {
      console.error("   :", error);
      alert("   .");
    }
  };

  return (
    <div className={style.signupContainer}>
      <div className={style.signupTitle}>
        ! <br />    JOBIS !
      </div>

      <hr className={style.topLine} />
      <table className={style.signupTable}>
        <tbody>
          <tr>
            <td className={style.title}></td>
            <td className={style.sub}>
              <input
                type="text"
                placeholder=""
                value={userId}
                onChange={handleUserIdChange}
                disabled={isIdAvailable}
              />
              <br /> 6~20,  ,  
              {isIdAvailable && (
                <span style={{ color: "green", marginLeft: "10px" }}>
                  *  .
                </span>
              )}
            </td>
            <td>
              <button
                className={style.checkButton2}
                type="button"
                onClick={checkDuplicateId}
                disabled={isIdAvailable}
              >
                
              </button>
            </td>
          </tr>
          <tr>
            <td className={style.title}></td>
            <td className={style.sub}>
              <input
                type="password"
                placeholder=""
                value={password}
                onChange={handlePasswordChange}
                onBlur={validatePassword} //    
                ref={passwordInputRef}
              />
              <br /> 8~16,  , ,   2  
            </td>
          </tr>

          <tr>
            <td className={style.title}></td>
            <td>
              <input
                type="password"
                placeholder=" "
                value={confirmPassword}
                onChange={handleConfirmPasswordChange}
              />
              <br />
              {confirmPasswordError && (
                <span style={{ color: "red" }}>{confirmPasswordError}</span>
              )}
            </td>
          </tr>
          <tr>
            <td className={style.title}></td>
            <td>
              <input
                type="text"
                placeholder=""
                value={initialEmail}
                readOnly
              />
              <div className={style.emailType}>
                *
                {snsType && (
                  <span>
                    {snsType === "kakao"
                      ? ""
                      : snsType === "naver"
                      ? ""
                      : snsType === "google"
                      ? ""
                      : ""}{" "}
                  </span>
                )}
                  .
              </div>
            </td>
            <td>
              <div>
                {snsType === "kakao" && (
                  <div className={style.snsTypeText}>
                    <img src={kakaoLogo} className={style.Logo} />
                    
                  </div>
                )}

                {snsType === "naver" && (
                  <div className={style.snsTypeText}>
                    <img src={naverLogo} className={style.Logo} />
                    
                  </div>
                )}

                {snsType === "google" && (
                  <div className={style.snsTypeText}>
                    <img src={googleLogo} className={style.Logo} />
                    
                  </div>
                )}
              </div>
            </td>
          </tr>
          <tr>
            <td className={style.title}></td>
            <td>
              <input
                type="text"
                placeholder=" "
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            </td>
          </tr>

          <tr>
            <td className={style.title}></td>
            <td className={style.sub}>
              <input
                type="text"
                placeholder=" 8"
                value={birthDate}
                onChange={handleBirthDateChange}
                maxLength={8}
              />
              <br />  : 20010628
              {birthDateError && (
                <span style={{ color: "red", marginLeft: "10px" }}>
                  {birthDateError}
                </span>
              )}
            </td>
            <td className={style.radioContainer}>
              <div className={style.male}>
                <input
                  type="radio"
                  id="male"
                  name="gender" //  name   
                  value="male" // value   
                  checked={gender === "male"} //     
                  onChange={(e) => setGender(e.target.value)} //  
                  className={style.hiddenRadio}
                />
                <label htmlFor="male" className={style.customRadio}></label>
              </div>
              <div className={style.female}>
                <input
                  type="radio"
                  id="female"
                  name="gender" //  name   
                  value="female" // value   
                  checked={gender === "female"} //     
                  onChange={(e) => setGender(e.target.value)} //  
                  className={style.hiddenRadio}
                />
                <label htmlFor="female" className={style.customRadio}></label>
                
              </div>
            </td>
          </tr>
          <tr>
            <td className={style.title}></td>
            <td className={style.sub}>
              <input
                type="text"
                placeholder="  '-' "
                value={phoneNumber}
                onChange={handlePhoneNumberChange}
                maxLength={11}
                disabled={isPhoneAvailable} //    
              />
              <br /> : 01012345678
              <span style={{ color: isPhoneAvailable ? "green" : "red" }}>
                {phoneMessage}
              </span>
              {phoneError && (
                <span style={{ color: "red", marginLeft: "10px" }}>
                  {phoneError}
                </span>
              )}
            </td>
            <td>
              <button
                className={style.checkButton2}
                type="button"
                onClick={handlePhoneCheck}
              >
                
              </button>
            </td>
          </tr>
        </tbody>
      </table>
      <hr className={style.bottomLine} />
      <div className={style.agreeCheck}>
        <div className={style.allCheck}>
          <input
            type="checkbox"
            checked={allChecked} //   
            onChange={handleAllCheck} //    
          />{" "}
              
        </div>
        <hr className={style.checkLine} />
        <div>
          <div>
            <input
              type="checkbox"
              checked={serviceAgreement} //   
              onChange={(e) =>
                handleIndividualCheck("service", e.target.checked)
              } //    
            />{" "}
               ()
          </div>
          <div>
            <input
              type="checkbox"
              checked={privacyPolicy} //     
              onChange={(e) =>
                handleIndividualCheck("privacy", e.target.checked)
              } //    
            />{" "}
                  ()
          </div>
          <div>
            <input
              type="checkbox"
              checked={marketingConsent} //    
              onChange={(e) =>
                handleIndividualCheck("marketing", e.target.checked)
              } //    
            />{" "}
                ()
          </div>
        </div>
      </div>

      <button
        className={style.signupBtn}
        onClick={handleSignup} //   
      >
        
      </button>
    </div>
  );
}

export default SNSSignup;
