import React, { useState, useEffect, useContext } from 'react';
import { AuthContext } from "../../AuthProvider";
import { useParams, useNavigate } from 'react-router-dom';
import styles from "./JobPostingDetail.module.css";
import JobPostingSubMenubar from "../../components/common/subMenubar/JobPostingSubMenubar";

const JobPostingDetail = () => {
  const { jobId } = useParams();
  const [jobDetails, setJobDetails] = useState(null);
  const { secureApiRequest } = useContext(AuthContext);
  const navigate = useNavigate(); // useNavigate  

  const fetchJobDetails = async () => {
    try {
      const response = await secureApiRequest(`/jobposting/search?&id=${jobId}`, { method: "GET" });
      const jobData = response.data.jobs.job[0];
      setJobDetails(jobData);
    } catch (error) {
      console.error("Error fetching job details:", error); //  
    }
  };

  useEffect(() => {
    fetchJobDetails();
  }, [jobId]);

  if (!jobDetails) {
    return <div>Loading...</div>;
  }

  //     
  const {
    position,
    company,
    salary,
    location,
    'posting-timestamp': postingTimestamp,
    'expiration-timestamp': expirationTimestamp,
    active,
    keyword,
    url,
  } = jobDetails;

  //  
  const postingDateFormatted = postingTimestamp
    ? new Date(postingTimestamp * 1000).toLocaleString()
    : " ";
  const expirationDateFormatted = expirationTimestamp
    ? new Date(expirationTimestamp * 1000).toLocaleString()
    : " ";


  //   
  const companyName = company?.detail?.name || " ";
  const companyUrl = company?.detail?.href || "#";
  const locationName = position?.location?.name.replace(/&gt;/g, '>') || " ";
  const jobType = position?.['job-type']?.name || " ";
  const salaryText = salary?.name || " ";
  const experienceLevel = position?.['experience-level']?.name || "";
  const industry = position?.industry?.name || " ";
  const jobMidCode = position?.['job-mid-code']?.name || " ";

  const handleGoBack = () => {
    navigate(-1); //   
  };

  return (
    <div>
      <JobPostingSubMenubar />
    <div className={styles.jobDetailContainer}>
      <h1>{position?.title || "  "}</h1>
      <button onClick={handleGoBack} className={styles.goBackButton}> </button> {/*    */}
      <p><strong>:</strong> <a href={companyUrl} target="_blank" rel="noopener noreferrer">{companyName}</a></p>
      <p><strong>:</strong> {locationName}</p>
      <p><strong>:</strong> {jobType}</p>
      <p><strong>:</strong> {salaryText}</p>
      <p><strong>:</strong> {experienceLevel}</p>
      <p><strong>:</strong> {industry}</p>
      <p><strong>:</strong> {jobMidCode}</p>
      <p><strong>:</strong> {postingDateFormatted}</p>
      <p><strong>:</strong> {expirationDateFormatted}</p>
      <p><strong>:</strong> {active ? "" : ""}</p>
      <p><strong>:</strong> {keyword || " "}</p>
      <p><strong>  :</strong> <a href={url} target="_blank" rel="noopener noreferrer"></a></p>
    </div>
    </div>
  );
};

export default JobPostingDetail;
