import React, { useEffect, useContext } from "react";
import { useNavigate } from "react-router-dom";
import style from "./KakaoLink.module.css";
import kakaoLogo from "../../assets/images/kakaotalk.png";
import { AuthContext } from "../../AuthProvider";

function KakaoLink() {
    const { secureApiRequest } = useContext(AuthContext);
    const navigate = useNavigate();

    useEffect(() => {
        let isMounted = true;

        const extractCode = () => {
            const queryString = window.location.search;
            const urlParams = new URLSearchParams(queryString);
            return urlParams.get("code");
        };

        const sendCodeToBackend = async (code) => {
            if (!isMounted) return;
        
            try {
                const uuid = localStorage.getItem("uuid"); // uuid 
                if (!uuid) {
                    alert("  .");
                    navigate("/login");
                    return;
                }
        
                const response = await secureApiRequest("/kakao/link", {
                    method: "POST",
                    body: JSON.stringify({ code, uuid }),
                });
        
                // Axios    
                console.log("Axios  :", response);
        
                const responseData = response.data; // data  JSON  
                console.log("  :", response.status);
                console.log(" :", responseData);
        
                if (response.status === 200 || responseData.success === "true") {
                    alert(responseData.message || "  .");
                    navigate("/updateUser");
                } else {
                    alert(responseData.error || "  .");
                }
            } catch (error) {
                console.error("   :", error);
                alert("  .  .");
                navigate("/updateUser");
            }
        };
        
        

        const code = extractCode();
        if (code) {
            sendCodeToBackend(code);
        }

        return () => {
            isMounted = false;
        };
    }, [navigate]);

    return (
        <div className={style.container}>
            <img src={kakaoLogo} className={style.kakaoLogo} />
            <div>   ...</div>
        </div>
    );
}

export default KakaoLink;
