import React, { useEffect, useState, useContext } from "react";
import { AuthContext } from "../../AuthProvider"; // AuthProvider Context 
import styles from './ReviewList.module.css';  //      
import { useNavigate } from "react-router-dom";
import Paging from "../../components/common/Paging"; // Paging  
import InsertButton from "../../components/common/button/InsertButton"; // InsertButton  

const ReviewList = () => {
    // AuthContext    API   
    const { isLoggedIn, isAuthInitialized, secureApiRequest, role } = useContext(AuthContext);
    
    const [reviewList, setReviewList] = useState([]);
    const [totalItems, setTotalItems] = useState(0);
    const [currentPage, setCurrentPage] = useState(1);
    const itemsPerPage = 10; //     
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState(null);
    const navigate = useNavigate();
  
    // 1.       /login 
  useEffect(() => {
    //      
    if (isAuthInitialized) {
      if (!isLoggedIn) {
        console.log("  .   .");
        navigate("/login"); //   
      }
    }
  }, [isLoggedIn, isAuthInitialized, navigate]);

  // 2. Review     
  const fetchReviewList = async (page = 1) => {
    //      
    if (!isLoggedIn) {
      return;
    }

    //       
    setIsLoading(true);
    setError(null);

    try {
      //      
      console.log(`Review   - : ${page}`);
      const response = await secureApiRequest(`/review?page=${page}&size=10`, {
        method: "GET",
    });

      //    
      console.log("Review   :", response.data);
      setReviewList(response.data.list || []); // Review  
      setTotalItems(response.data.paging?.totalItems || 0); //    
      setCurrentPage(page); //   
    } catch (err) {
      console.error("Review   :", err);
      setError("    ."); //   
    } finally {
      //   
      setIsLoading(false);
    }
  };

  // 3.   currentPage    
  useEffect(() => {
    if (isLoggedIn) {
      fetchReviewList(currentPage); // Review   
    }}, [isLoggedIn, currentPage]);

  // 4. " "      
  const handleInsertClick = () => {
    navigate("/review/insert");
  };

  // 5. Review        
  const handleDetailClick = (rno) => {
    console.log("Navigating to:", `/review/detail/${rno}`);
    navigate(`/review/detail/${rno}`); // Review   
  };

  // 6.      
  if (error) {
    return <p className={styles.error}>{error}</p>;
  }

  // 7. Review  
  return (
    <div className={styles["review-list-container"]}>
      <h1>Review </h1>
      {isLoading ? (
        //     
        <p>  ...</p>
      ) : (
        <>
          <table className={styles["review-table"]}>
            <thead>
              <tr>
                <th>No</th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {reviewList.length > 0 ? (
                // Review      
                reviewList.map((review, index) => (
                  <tr key={review.rno} onClick={() => handleDetailClick(review.rno)}>
                    <td>{index + 1 + (currentPage - 1) * itemsPerPage}</td>
                    <td>{review.rtitle}</td>
                    <td>{review.rwriter}</td>
                    <td>{new Date(review.rwdate).toLocaleDateString()}</td>
                  </tr>
                ))
              ) : (
                //       
                <tr>
                  <td colSpan="4"> .</td>
                </tr>
              )}
            </tbody>
          </table>
          {/*   */}
          <Paging
            totalItems={totalItems}
            itemsPerPage={itemsPerPage}
            currentPage={currentPage}
            onPageChange={(page) => setCurrentPage(page)} //    
          />
        </>
      )}
      {role === "USER" && (
        // USER   " "  
        <div className={styles.buttonContainer}>
        <InsertButton onClick={handleInsertClick} label=" "/>
      </div>
      )}
    </div>
  );
};


export default ReviewList;